.cont-eight {
  .billing {
    h2 {
      font-size: 24px;
      line-height: 44px;
      color: $primaryColor;
      padding: 10px 40px;
      border-bottom: 1px solid rgba(#e3e0d3, 0.4);
      @media #{$mobile} {
        padding: 8px 16px;
      }
    }
    .renew-msg {
      background: #ffecf1;
      border-radius: 10px;
      margin: 40px 0 30px;
      display: flex;
      align-items: center;
      padding: 20px 15px;
      @media #{$maxTab} {
        margin: 0 0 30px;
      }
      @media #{$mobile} {
        padding: 10px 15px;
      }
      svg {
        margin-right: 15px;
        min-width: 26px;
        margin-left: unset;
      }
      p {
        font-size: 16px;
        line-height: 20px;
        color: $primaryColor;
        @media (max-width: 1199px) {
          font-size: 14px;
        }
        @media #{$mobile} {
          font-size: 12px;
        }
      }
    }
  }
  .subscription {
    border-top: 10px solid $primaryColor;
    margin: 30px 0;
    overflow: visible;
    .subscritpion-info {
      padding: 25px 40px 30px;
      display: flex;
      align-items: center;
      @media (max-width: 1199px) {
        padding: 25px 20px 30px;
      }
      @media #{$mobile} {
        padding: 24px 16px 30px;
        flex-direction: column;
        align-items: flex-start;
      }
      .extend {
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 50%;
        @media #{$mobile} {
          width: 100%;
          flex-direction: column;
          align-items: flex-start;
        }
        p {
          font-size: 16px;
          line-height: 20px;
          color: #8d8b92;
          margin-bottom: 10px;
        }
        h3 {
          font-weight: 500;
          font-size: 20px;
          line-height: 20px;
          color: #5a5761;
          @media (min-width: 768px) and (max-width: 1199px) {
            font-size: 18px;
          }
        }
        a {
          font-size: 18px;
          width: 125px;
          height: 50px;
          line-height: 50px;
          @media (max-width: 1199px) {
            font-size: 16px;
          }
          @media #{$mobile} {
            min-height: 50px;
            width: 100%;
            margin-top: 30px;
          }
        }
      }
      .divider-line {
        border-right: 1px dashed #dbd9d3;
        margin: 0 22px;
        height: 70px;
        @media #{$mobile} {
          border-top: 1px dashed #dbd9d3;
          margin: 30px 0;
          height: 1px;
          width: 100%;
        }
      }
      .renewal {
        display: flex;
        align-items: center;
        justify-content: space-between;
        flex: 1;
        position: relative;
        @media #{$mobile} {
          width: 100%;
        }
        h3.title {
          font-weight: 500;
          font-size: 16px;
          line-height: 20px;
          color: #5a5761;
          margin-bottom: 8px;
          display: flex;
          &.statusOn {
            img.on {
              display: inline-block;
            }
            img.off {
              display: none;
            }
          }
          &.statusOff {
            img.off {
              display: inline-block;
            }
            img.on {
              display: none;
            }
          }
          img {
            margin-left: 12px;
          }
        }
        .renewal-text {
          font-size: 14px;
          line-height: 20px;
          color: #8d8b92;
          max-width: 290px;
          @media (max-width: 450px) {
            max-width: 210px;
          }
        }
        
        .auto-renewal {
          
          .menu-btn {
            width: 36px;
            height: 36px;
            border-radius: 50%;
            border: 1px solid transparent;
            background: #fefefe;
            transition: all 0.3s ease-in-out;
            display: flex;
            svg {
              margin: auto;
            }
            &:hover {
              border: 1px solid #ddd;
            }
          }
          .cancel {
            font-size: 14px;
            line-height: 20px;
            color: #5a5761;
            padding: 11px 16px;
            background: #FFFFFF;
            box-shadow: 0px 2px 13px rgba(0, 0, 0, 0.1);
            border-radius: 5px;
            position: absolute;
            right: 0;
            top: 75px;
            display: none;
            transition: all 0.2s ease-in-out;
            z-index: 444;
            @media #{$mobile} {
              top: 85px;
            }
            &:before {
              content: "";
              border-left: 10px solid transparent;
              border-top: 10px solid transparent;
              border-bottom: 10px solid #fff;
              border-right: 10px solid transparent;
              position: absolute;
              top: -19px;
              right: 10px;
            }
          }
        }
      }
    }
  }
  .update-payment {
    margin-bottom: 30px;
    h2 {
      @media #{$mobile} {
        padding: 16px;
        line-height: 120%;
      }
    }
    .payment-content {
      display: flex;
      justify-content: space-between;
      padding: 30px;
      @media #{$mobile} {
        padding: 30px 15px;
        flex-direction: column;
      }
    }
    .old-payment-method {
      width: 30%;
      @media #{$mobile} {
        width: 100%;
        padding-bottom: 30px;
      }
      h3 {
        font-weight: 500;
        font-size: 20px;
        line-height: 20px;
        color: #5A5761;
        margin-bottom: 25px;
      }
      .label {
        font-weight: bold;
        font-size: 12px;
        line-height: 20px;
        color: rgba(#5B5863, 0.6);
        border-bottom: 1px solid #F4F3EE;
        margin-bottom: 10px;
      }
      .visa {
        font-weight: bold;
        font-size: 16px;
        line-height: 20px;
        color: #9D9BA1;
        margin: 6px 0 30px;
      }
      span {
        font-weight: 500;
        font-size: 24px;
        line-height: 20px;
        color: #FD3E72;
      }
    }
    .update-payment-method {
      width: 65%;
      border-left: 1px dashed #DBD9D3;
      padding-left: 30px;
      @media #{$mobile} {
        width: 100%;
        padding-left: 0;
        border-top: 1px dashed #DBD9D3;
        padding-top: 30px;
        border-left: 0
      }
      p {
        font-size: 16px;
        line-height: 20px;
        color: #8D8B92;
        margin-bottom: 32px;
        max-width: 560px;
        @media #{$mobile} {
          font-size: 14px;
          line-height: 160%;
          color: #8D8B92;
        }
      }
      .select2-container--default .select2-selection--single .select2-selection__rendered {
        padding-left: 20px;
      }
      .padd-right {
        padding-right: 10px;
        @media #{$tablet} {
          padding-right: 5px;
        }
        @media #{$mobile} {
          padding-right: 15px;
        }
      }
      .padd-left {
        padding-left: 10px;
        @media #{$tablet} {
          padding-left: 5px;
        }
        @media #{$mobile} {
          padding-left: 15px;
        }
      }
      form {
        .select-container {
          margin-bottom: 20px;
          @media #{$maxTab} {
            margin-bottom: 10px;
          }
        }
        .form-group {
          border: 1px solid #e0ded5;
          padding: 4px 20px 7px;
          margin-bottom: 20px;
          height: 64px;
          border-radius: 5px;
          @media #{$maxTab} {
            margin-bottom: 10px;
          }
          label {
            font-size: 12px;
            line-height: 20px;
            font-weight: 500;
            margin-bottom: 2px;
            position: relative;
          }
          input {
            border: 0;
            padding: 0;
            font-weight: 500;
            font-size: 14px;
            width: 100%;
            &::placeholder {
              font-weight: normal;
            }
          }
        }
        .update-payment-btn {
          height: 50px;
          line-height: 50px;
          min-height: 50px;
          font-size: 16px;
          width: 175px;
          float: right;
          @media #{$mobile} {
            display: block;
            width: 100%;
            margin-top: 10px;
          }
          
        }
      }
    }
  }
  .billing-history {
    @media #{$mobile} {
      h2 {
        padding: 13px 16px;
      }
    }
    .no-invoices {
      background: #FAFAFA;
      font-weight: 500;
      font-size: 16px;
      line-height: 20px;
      text-align: center;
      color: #FD3E72;
      padding: 20px;
    min-height: 100px;
    }
    .billing-table {
      padding: 10px 30px 30px;
      width: 100%;
    }
    table {
      background-color: #fff;
      th {
        border: 0;
      }

      td {
        border: 0;
        font-size: 14px;
        color: #5a5761;
        height: 64px;
        padding-left: 10px;
        vertical-align: middle;
      }

      thead {
        tr {
          th {
            font-size: 16px;
            text-transform: capitalize;
            border-bottom: 0;
            height: 56px;
            color: #5b5863;
            vertical-align: middle;
            padding-left: 10px;
            @media #{$tablet} {
              font-size: 14px;
            }
          }
        }
      }

      tbody {
        tr {
          &:nth-child(odd) {
            background: #fafafa;
          }
        }

        th[scope="row"] {
          font-size: 14px;
          color: #8d8b92;
          padding: 0 30px 0 32px;
          font-weight: normal;
          vertical-align: middle;
        }

        td:nth-child(2) {
          a {
            color: $primaryColor;
            font-weight: 500;
            text-decoration: underline !important;
          }
        }
        td:last-child {
          a {
            display: inline-block;
            box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.15);
          }
        }
      }
    }
    .invoices {
      > .cell {
        background-color: #fff;
        position: relative;
        padding: 0 18px;
        border-bottom: 1px solid rgba(#e3e0d3, 0.4);
        height: 60px;
        line-height: 60px;

        > span {
          font-size: 18px;
          line-height: 20px;
          font-weight: 500;
          color: #5b5863;
        }

        .arrow {
          top: 22px;
          right: 28px;
        }

        &.active {
          border-bottom: 0;
          .arrow {
            span {
              background-color: #5b5863;

              &:first-of-type {
                transform: rotate(-45deg);
              }

              &:last-of-type {
                transform: rotate(45deg);
              }
            }
          }
        }
      }

      .toggle {
        background: #fafafa;
        box-shadow: inset 0px 4px 4px #f6f6f6;
        padding: 0 14px;
        display: none;

        .cell {
          padding: 0 14px;
          line-height: 52px;
          &.download_bill {
            padding: 12px 0;
          }
          .download_receipt {
            display: block;
            min-height: 50px;
            line-height: 50px;
          }
          @media #{$mobile} {
            padding: 0 5px;
          }
          &:not(:last-child) {
            border-bottom: 1px solid #f4f4f4;
          }

          p {
            font-size: 14px;
            color: rgba(#5b5863, 0.6);
            font-weight: 500;
            span.id,
            span.amount,
            span.subscripe,
            span.payment,
            span.payment {
              color: #5a5761;
              opacity: 0.6;
              font-weight: 400;
              float: right;
            }
          }
        }
      }
    }
  }
}

#cancel-autorenewal {
  .modal-title {
    font-size: 20px;
    line-height: 44px;
    border: 1px solid rgba(#e3e0d3, 0.4);
    padding: 7px;
  }
  .cancel-reasons-content {
    padding: 16px;
  }

  .cancel_autorenewal {
    display: none;
    &__content {

      padding: 25px 20px;
      @media #{$mobile} {
        padding: 15px;
      }

      h3 {
        padding-bottom: 20px;
        border-bottom: 1px solid rgba(#e3e0d3, 0.4);
      }
      .image {
        border-bottom: 1px solid rgba(#e3e0d3, 0.4);
      }

      .radio-gp {
        label {
          cursor: pointer;
          margin-left: 5px;
          color: #5B5863;
        }
        input[type="radio"] {
          cursor: pointer;
          height: 20px;
          width: 20px;
          opacity: 1;
          appearance: none;
          border: 2px solid #DEDEDE;
          border-radius: 3px;
          background: #fff;
          position: relative;
        }
        
        input[type="radio"]:checked {
          border: 2px solid $primaryColor;
        }

        input[type="radio"]:checked + label {
          font-weight: 500;
        }
        
        input[type="radio"]:checked:before, input[type="radio"]:checked:after {
          content: "\2714";
          color: $primaryColor;
          position: absolute;
          top: -2px;
          left: 2px;
          font-size: 14px;
        }
      }
      
      .cancel-warning {
        padding: 15px;
        margin: 20px 0;
        background: #FFECF1;
        border-radius: 10px;
        .head {
          border-bottom: 1px solid #FFD8E3;
          padding-bottom: 10px;
          margin-bottom: 10px;
        }
      }
      .cancel-autorenewal-btns {
        a {
          font-size: 16px;
          padding: 12px 8px !important;
          min-height: auto;
          border: 1px solid $primaryColor;
          line-height: unset;
          @media #{$mobile} {
            line-height: 20px;
            font-size: 14px;
          }
          &:first-child {
            margin-bottom: 12px;
          }
          &.white_btn {
            color: #5B5863;
            border: 1px solid rgba(#5B5863, 0.3);
          }
        }
      }
    }
  }
  .reason-item {
    overflow: hidden;
    background-color: #fff;
    border-radius: 10px;
    border: 1px solid #f0f0f0;
    @media #{$mobile} {
      border-radius: 5px;
    }
    &.no-border-radius-bottom {
      border-radius: 10px 10px 0 0;
      @media #{$mobile} {
        border-radius: 5px 5px 0 0;
      }
    }

    &:not(:first-child) {
      margin-top: 5px;

      @media #{$mobile} {
        margin-bottom: 0;
      }
    }
    .reason-text {
      input[type="radio"] {
        display: none;
        &:checked {
          & + label:before {
            border-color: $primaryColor;
          }
          & + label:after {
            opacity: 1;
            visibility: visible;
          }
        }
      }
      .label-radio {
        font-weight: 500;
        font-size: 14px;
        line-height: 26px;
        position: relative;
        cursor: pointer;
        display: block;
        margin-bottom: 0;
        padding-left: 45px;
        z-index: 2;
        @media #{$mobile} {
          padding-left: 35px;
        }
        span {
          display: inline-block;
          padding: 7px 0;
        }

        &:before {
          content: "";
          position: absolute;
          top: 50%;
          left: 16px;
          width: 16px;
          height: 16px;
          border-radius: 50%;
          border: 2px solid #cccac2;
          transform: translateY(-50%);
          @media #{$mobile} {
            left: 12px;
          }
        }

        &:after {
          top: 50%;
          left: 20px;
          content: "";
          display: block;
          position: absolute;
          width: 8px;
          height: 8px;
          border-radius: 50%;
          background-color: #fd3e72;
          transition: all 0.3s ease-in-out;
          opacity: 0;
          visibility: hidden;
          transform: translateY(-50%);
          @media #{$mobile} {
            left: 16px;
          }
        }
      }
    }
  }
  .reason-info {
    background-color: #fff;
    padding: 20px 10px 5px;
    border: 1px solid #f0f0f0;
    border-top: 0;
    border-radius: 0 0 10px 10px;
    display: none;
    text-align: center;
    @media #{$mobile} {
      border-radius: 0 0 5px 5px;
    }

    @media #{$maxTab} {
      padding: 20px 16px 5px;
    }

    img {
      margin-bottom: 15px;
    }
    p {
      font-size: 14px;
      line-height: 20px;
      margin-bottom: 15px;
    }
    .reason-btns {
      display: flex;
      justify-content: space-between;
      @media #{$mobile} {
        flex-direction: column;
      }
      a {
        font-size: 14px;
        padding: 9px 0;
        width: 49%;
        line-height: 23px;
        @media #{$mobile} {
          width: 100%;
          &.support-link {
            display: block;
            width: 100%;
            min-height: 42px;
            line-height: 42px;
            margin-bottom: 10px;
          }
        }
        &.white_btn {
          border: 1px solid $primaryColor;
        }
      }
    }
    .renewal-off {
      font-size: 14px;
      line-height: 56px;
      text-decoration-line: underline !important;
      color: #5b5863;
      opacity: 0.7;
      transition: all 0.3s ease-in-out;
      font-weight: 500;
      &:hover {
        opacity: 1;
        text-decoration: none !important;
      }
    }
  }

  .skip {
    background: #ffffff;
    border: 1px solid rgba(#5B5863, 0.3);
    border-radius: 5px;
    height: 50px;
    line-height: 50px;
    width: 180px;
    text-align: center;
    font-weight: 500;
    transition: all 0.3s ease-in-out;
    display: block;
    margin: 0 auto 20px;
    font-size: 18px;
    color: #5B5863;
    @media #{$maxTab} {
      height: 42px;
      line-height: 42px;
      font-size: 16px;
    }
    &:hover {
      color: $primaryColor;
    }
  }
}


#active-renewal {
  .modal-dialog {
    margin: 16px;
    @media (min-width: 576px) {
      max-width: 430px;
      margin: auto;
    }
  }
  .active-renewal-content {
    padding: 30px;
    @media #{$mobile} {
      padding: 45px 15px 20px;
    }
    img {
      @media #{$mobile} {
        width: 160px;
        margin: auto;
      }
    }
    h3 {
      font-size: 20px;
      line-height: 120%;
      margin: 30px 0 20px;
      text-align: center;
      @media #{$mobile} {
        margin: 25px 0 15px;
        font-size: 18px;
      }
    }
    p {
      font-size: 14px;
      line-height: 20px;
      text-align: center;
      color: #5a5761;
      opacity: 0.7;
      margin-bottom: 40px;
      @media #{$mobile} {
        margin-bottom: 25px;
      }
    }

    button {
      display: block;
      width: 100%;
      height: 56px;
      line-height: 56px;
      padding: 0;
      @media #{$mobile} {
        height: 50px;
        line-height: 50px;
        min-height: 50px;
        font-size: 18px;
      }
    }
  }

}
