.cont-four {
  .dashboard-weplay {
    margin: 40px 0 30px;
    display: flex;
    @media #{$maxTab} {
      margin-top: 0;
    }
    @media #{$mobile} {
      flex-direction: column;
    }
    .weplay-submission {
      width: 50%;
      padding: 40px 50px;
      @media (min-width: 768px) and (max-width: 1199px) {
        padding: 30px;
      }
      @media #{$mobile} {
        order: 2;
        width: 100%;
        padding: 40px 18px 24px;
      }
      h2 {
        font-size: 26px;
        line-height: 36px;
        text-align: center;
        @media #{$mobile} {
          font-size: 24px;
        }
      }
      > p {
        font-weight: 500;
        font-size: 16px;
        line-height: 22px;
        text-align: center;
        margin: 20px auto 33px;
        position: relative;
        max-width: 470px;
        @media #{$mobile} {
          font-size: 14px;
          padding: 0 5px;
        }
        &:after {
          content: "";
          position: absolute;
          width: 80%;
          display: block;
          border-bottom: 1px solid #f4f4f4;
          bottom: -30px;
          left: 50%;
          transform: translateX(-50%);
        }
      }
      form {
        padding-top: 24px;
        @media #{$mobile} {
          padding-top: 40px;
        }
        .form-group {
          margin-bottom: 30px;
          @media #{$mobile} {
            margin-bottom: 25px;
          }

          label {
            display: block;
            font-weight: 500;
            font-size: 18px;
            line-height: 20px;
            color: #5a5761;
            margin-bottom: 9px;
            @media #{$mobile} {
              font-size: 14px;
              margin-bottom: 5px;
            }
          }
          input[type="text"] {
            height: 64px;
            line-height: 64px;
            border: 1px solid #e0ded5;
            border-radius: 5px;
            color: #5a5761;
            padding-left: 30px;
            width: 100%;
            font-weight: 500;
            font-size: 18px;
            @media #{$mobile} {
              padding-left: 18px;
              font-size: 16px;
            }
            &::placeholder {
              font-weight: normal;
              font-size: 14px;
              line-height: 64px;
              color: #8d8b92;
            }
          }
        }
        .select2-container--default .select2-search--dropdown .select2-search__field {
          display: block;
        }
        .service-supported {
          background: rgba(8, 174, 126, 0.1);
          border-radius: 5px;
          padding: 11px 17px;
          display: flex;
          align-items: center;
          @media (max-width: 1199px) {
            align-items: flex-start;
          }
          span {
            margin-right: 12px;
          }
          .text {
            font-size: 14px;
            line-height: 20px;
            color: #5b5863;
            opacity: 0.7;
            a {
              color: $primaryColor;
              font-weight: 500;
            }
          }
        }
        .weplay-btns {
          display: flex;
          justify-content: center;
          margin-top: 25px;
          @media #{$mobile} {
            flex-direction: column;
          }
          button {
            width: 217px;
            height: 50px;
            line-height: 50px;
            font-size: 16px;
            margin-right: 30px;
            @media (min-width: 768px) and (max-width: 1199px) {
              margin-right: 10px;
            }
            @media #{$mobile} {
              margin: 0;
              width: 100%;
              min-height: 50px;
            }
          }
          a {
            width: 217px;
            height: 50px;
            line-height: 50px;
            font-weight: 500;
            font-size: 16px;
            display: inline-block;
            text-align: center;
            color: $primaryColor;
            border: 1px solid transparent;
            transition: all 0.3s ease-in-out;
            border-radius: 5px;
            @media #{$mobile} {
              display: block;
              width: 100%;
              margin-top: 12px;
            }
            &:hover {
              border-color: $primaryColor;
            }
          }
        }
      }
    }
    .weplay-info {
      width: 50%;
      background: linear-gradient(136.27deg, #fe7e56 3.38%, #fd3e72 54.98%, #49a8ff 160.06%);
      border-radius: 0px 10px 10px 0px;
      text-align: center;
      padding: 40px 40px 0;
      color: #fff;
      position: relative;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: center;
      @media (min-width: 768px) and (max-width: 1199px) {
        padding: 30px 30px 0;
      }
      @media #{$mobile} {
        background: linear-gradient(166.7deg, #fe7e56 13.51%, #fd3e72 63.66%, #49a8ff 165.8%);
        order: 1;
        width: 100%;
        padding: 40px 16px 0;
        border-radius: 0;
      }
      h2 {
        font-weight: 500;
        font-size: 24px;
        line-height: 30px;
        max-width: 400px;
        margin: auto;
      }
      p {
        font-size: 14px;
        line-height: 20px;
        margin: 30px auto;
        max-width: 450px;
        strong {
          font-weight: 500;
        }
      }
      a {
        background: #fafafa;
        border-radius: 5px;
        padding: 0 28px;
        height: 50px;
        line-height: 50px;
        color: $primaryColor;
        font-weight: 500;
        margin-bottom: 10px;
        display: inline-block;
        margin: auto;
        @media #{$mobile} {
          margin: 0 auto 20px;
          display: block;
          width: 100%;
        }
        svg {
          margin-left: 5px;
          transition: all 0.3s ease-in-out;
        }
        &:hover {
          svg {
            transform: translateX(4px);
          }
        }
      }
    }
    .weblay_submit_msg {
      display: none;
      width: 50%;
      padding: 40px 60px;
      text-align: center;
      @media #{$mobile} {
        order: 2;
        width: 100%;
        padding: 40px 16px;
      }
      .content {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
      }
      h2 {
        font-size: 26px;
        line-height: 36px;
        color: $primaryColor;
        margin: 32px 0 14px;
        @media #{$mobile} {
          font-size: 20px;
          line-height: 120%;
        }
      }
      p {
        font-size: 16px;
        line-height: 22px;
        max-width: 340px;
        margin: 0 auto 35px;
        @media #{$mobile} {
          font-size: 14px;
        }
      }
      button {
        width: 220px;
        height: 50px;
        line-height: 50px;
        font-size: 16px;
        @media #{$maxTab} {
          width: 100%;
          min-height: 50px;
        }
      }
    }
  }
  .weplay-services {
    border-top: 10px solid $primaryColor;
    .box-title {
      line-height: 1;
      height: auto;
      h2 {
        padding-top: 15px;
        padding-bottom: 15px;
        font-size: 20px;
        line-height: 28px;
      }
    }
    &-content {
      padding: 30px 35px;
      @media #{$mobile} {
        padding: 14px 10px;
      }
    }
    .services {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      .service {
        background: #ffffff;
        border: 1px solid #e1e1e1;
        border-radius: 10px;
        height: 170px;
        width: 170px;
        margin: 0 11px 25px;
        position: relative;
        @media (min-width: 1600px) {
          margin: 0 15px 30px;
        }
        &.service-tooltip {
          cursor: pointer;
          position: relative;
          transition: all 0.3s ease-in-out;
          &:hover {
            border: 1px solid $primaryColor;
            .tooltiptext {
              opacity: 1;
              visibility: visible;
              top: -30px;
            }
          }
          .tooltiptext {
            position: absolute;
            display: inline-block;
            background: #4f4f4f;
            color: #ffffff;
            padding: 7px 14px 10px;
            font-weight: 500;
            font-size: 14px;
            text-align: center;
            line-height: 17px;
            top: -35px;
            transform: translateX(-50%);
            left: 50%;
            opacity: 0;
            visibility: hidden;
            border-radius: 5px;
            transition: all 0.3s ease-in-out;
            width: 135px;
          }

          .tooltiptext:after {
            content: "";
            position: absolute;
            border-width: 8px;
            border-style: solid;
            border-color: #4f4f4f transparent transparent transparent;
            display: block;
            bottom: -16px;
            left: 50%;
            transform: translateX(-50%);
            z-index: 1;
          }
        }
        .label {
          position: absolute;
          background-color: $primaryColor;
          width: 95px;
          height: 24px;
          line-height: 24px;
          color: #fff;
          font-weight: 500;
          bottom: -12px;
          left: 50%;
          transform: translateX(-50%);
          border-radius: 3px;
          text-align: center;
          font-size: 14px;
          z-index: 0;
          cursor: pointer;
          @media #{$mobile} {
            width: 77px;
            font-size: 12px;
          }
          &.label-dark {
            background-color: #5b5863;
            cursor: text;
          }
          &:hover {
            + .libraries-tooltiptext {
              opacity: 1;
              visibility: visible;
              top: 116%;
            }
          }
        }
        .libraries-tooltiptext {
          position: absolute;
          background: #4f4f4f;
          width: 195px;
          font-size: 12px;
          line-height: 14px;
          color: #ffffff;
          padding: 10px 10px 0;
          top: 125%;
          left: 50%;
          transform: translateX(-50%);
          opacity: 0;
          visibility: hidden;
          border-radius: 5px;
          transition: all 0.3s ease-in-out;
          z-index: 1;
          @media #{$tablet} {
            width: 145px;
          }
          @media #{$mobile} {
            width: 115px;
          }
          li {
            display: flex;
            align-items: center;
            margin-bottom: 15px;
            text-align: left;
            img {
              margin-right: 10px;
              width: 24px;
              @media #{$mobile} {
                width: 20px;
                margin-right: 5px;
              }
            }
          }
        }

        .libraries-tooltiptext:after {
          content: "";
          position: absolute;
          border-width: 8px;
          border-style: solid;
          border-color: transparent transparent #4f4f4f transparent;
          display: block;
          top: -15px;
          left: 50%;
          transform: translateX(-50%);
          z-index: 1;
        }
        @media (min-width: 768px) and (max-width: 1199px) {
          width: 140px;
          min-width: 140px;
          max-width: 140px;
          margin: 0 6px 20px;
          height: 140px;
        }
        @media #{$mobile} {
          width: 95px;
          height: 95px;
          max-width: 95px;
          min-width: 95px;
          margin: 0 5px 20px;
          display: flex;
          flex-direction: column;
          justify-content: space-evenly;
          text-align: center;
          align-items: center;
          padding-top: 10px;
        }
        .icon {
          height: 65%;
          position: relative;
          @media #{$mobile} {
            height: auto;
            margin-bottom: 10px;
          }
          img {
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
            @media #{$mobile} {
              transform: translate(-50%, -50%) scale(0.6);
            }
            @media (min-width: 768px) and (max-width: 1199px) {
              transform: translate(-50%, -50%) scale(0.8);
            }
          }
        }
        p {
          height: 35%;
          font-size: 14px;
          line-height: 18px;
          text-align: center;
          padding: 0 15px;
          @media #{$mobile} {
            font-size: 12px;
            padding: 0;
            height: auto;
          }
        }
      }
    }
  }
}