.cont-seven {
  .earn-free-months {
    .share-link {
      margin-bottom: 30px;
      margin-top: 40px;
      @media #{$maxTab} {
        margin-top: 0;
      }
      .share-link-heading {
        display: flex;
        align-items: center;
        justify-content: space-between;
        border-bottom: 1px solid rgba(#e3e0d3, 0.4);
        padding: 13px 35px;
        @media (max-width: 1399px) {
          padding: 0;
          border-bottom: 0;
          display: block;
        }
        h3 {
          font-size: 24px;
          line-height: 44px;
          @media (max-width: 1399px) {
            border-bottom: 1px solid rgba(#e3e0d3, 0.4);
          }
          @media (min-width: 768px) and (max-width: 1399px) {
            padding: 15px 35px;
          }
          @media #{$mobile} {
            font-size: 20px;
            padding: 13px 16px;
          }
        }
        p {
          font-size: 14px;
          line-height: 20px;
          color: #8d8b92;
          @media (min-width: 768px) and (max-width: 1399px) {
            padding: 20px 35px 0;
          }
          @media #{$mobile} {
            font-size: 12px;
            padding: 20px 16px;
          }
        }
      }
      .referral-content {
        padding: 25px 35px;

        @media #{$mobile} {
          padding: 0 15px 25px;
        }
      }
      .referral-link {
        display: flex;
        margin-bottom: 25px;
        @media #{$mobile} {
          flex-direction: column;
          margin-bottom: 40px;
        }
        input {
          flex: 1;
          font-weight: 500;
          font-size: 16px;
          height: 64px;
          line-height: 64px;
          padding: 0 25px;
          background: #ffffff;
          border: 1px solid #e0ded5;
          border-radius: 5px;

          @media #{$mobile} {
            height: auto;
            padding: 0 17px;
            margin-bottom: 18px;
          }
        }
        .copy-link {
          position: relative;
          width: 206px;
          height: 64px;
          line-height: 64px;
          margin-left: 10px;
          font-weight: 500;
          font-size: 16px;
          @media #{$betweentabdesk} {
            width: 180px;
          }
          @media #{$mobile} {
            width: 100%;
            margin-left: 0;
          }
          svg {
            margin-right: 5px;
          }
          &:hover {
            .tooltiptext {
              visibility: visible;
              opacity: 1;
            }
          }
          .tooltiptext {
            visibility: hidden;
            background-color: #4f4f4f;
            font-size: 12px;
            color: #fff;
            text-align: center;
            border-radius: 5px;
            padding: 3px 11px;
            position: absolute;
            z-index: 1;
            bottom: 107%;
            left: 50%;
            transform: translatex(-50%);
            opacity: 0;
            transition: opacity 0.3s;
            font-weight: 500;
            text-transform: none;
            line-height: 1.3;
            @media #{$mobile} {
              font-size: 12px;
              padding: 4px 8px;
            }

            &.changed {
              background-color: #08ae7e;
              &:after {
                border-color: #08ae7e transparent transparent transparent;
              }
            }
            &:after {
              content: "";
              position: absolute;
              top: 100%;
              left: 50%;
              margin-left: -5px;
              border-width: 5px;
              border-style: solid;
              border-color: #4f4f4f transparent transparent transparent;
            }
          }
        }
      }
      .social-shares {
        display: flex;
        align-items: center;
        justify-content: center;
        @media #{$mobile} {
          flex-direction: column;
        }
        button {
          border: 1px solid $primaryColor;
          border-radius: 5px;
          text-align: center;
          background-color: #fff;
          width: 216px;
          height: 56px;
          line-height: 56px;
          color: $primaryColor;
          font-weight: 500;
          @media #{$betweentabdesk} {
            width: 180px;
          }
          @media #{$mobile} {
            width: 100%;
          }
          &:not(:last-child) {
            margin-right: 15px;
            @media #{$mobile} {
              margin-right: 0;
              margin-bottom: 20px;
            }
          }
        }
      }
    }
    .invites {
      .invites-info {
        @media #{$maxTab} {
          margin-bottom: 30px;
        }
        ul {
          text-align: center;
          padding: 10px 20px 0;
          @media #{$mobile} {
            padding: 10px 10px 0;
          }
          li {
            display: flex;
            justify-content: space-between;
            align-items: center;
            padding: 15px;
            @media (min-width: 1200px) and (max-width: 1400px) {
              padding: 15px 0;
            }
            @media #{$betweentabdesk} {
              padding: 15px 0;
              flex-wrap: wrap;
              justify-content: center;
            }
            &:not(:last-child) {
              border-bottom: 1px solid #e3e0d3;
            }
            .icon {
              width: 44px;
              height: 44px;
              background-color: #fafafa;
              border-radius: 5px;
              display: flex;
              @media #{$betweentabdesk} {
                margin-right: 20px;
              }
              img {
                margin: auto;
              }
            }
            h3 {
              font-size: 50px;
              line-height: 60px;
              &.red {
                color: $primaryColor;
              }
            }
            p {
              font-size: 16px;
              line-height: 20px;
              text-transform: uppercase;
              color: #8d8b92;
              max-width: 110px;
              text-align: left;
              @media #{$betweentabdesk} {
                max-width: unset;
                font-size: 14px;
              }
              @media (min-width: 1200px) and (max-width: 1400px) {
                max-width: 100px;
                font-size: 14px;
              }
              @media #{$tablet} {
                max-width: 225px;
                width: 205px;
                text-align: right;
              }
            }
          }
        }
        .note {
          display: flex;
          background: linear-gradient(98.67deg, #fe7e56 -2.79%, #fd3e72 54.21%, #49a8ff 170.28%);
          border-radius: 0px 0px 10px 10px;
          padding: 30px 40px;
          color: #fff;
          @media #{$betweentabdesk} {
            padding: 30px 15px;
          }
          @media #{$mobile} {
            padding: 30px;
          }
          > span {
            font-size: 40px;
            line-height: 20px;
            margin-right: 10px;
          }
          p {
            font-size: 14px;
            line-height: 20px;
          }
        }
      }
      .invite {
        background: #ffffff;
        border-radius: 10px;
        padding: 26px 30px;
        @media #{$betweentabdesk} {
          padding: 15px 20px;
        }
        @media #{$mobile} {
          padding: 20px 16px 10px;
        }

        .top {
          padding-bottom: 30px;
          @media #{$betweentabdesk} {
            padding-bottom: 20px;
          }
          h3 {
            font-size: 24px;
            line-height: 44px;
            margin-bottom: 10px;
            @media #{$betweentabdesk} {
              font-size: 22px;
            }
            @media #{$mobile} {
              font-size: 20px;
              line-height: 26px;
              margin-bottom: 20px;
            }
          }
          form {
            .inputs {
              display: flex;
              justify-content: space-between;
              align-items: flex-start;
              flex-wrap: wrap;
              @media #{$mobile} {
                flex-direction: column;
              }
              > div {
                width: 49%;
                @media #{$mobile} {
                  width: 100%;
                }
              }
              .form-group {
                border: 1px solid #e0ded5;
                padding: 15px 30px;
                margin-bottom: 20px;
                transition: all 0.3s ease-in-out;
                border-radius: 5px;
                background-color: #fff;
                height: 80px;
                @media #{$betweentabdesk} {
                  padding: 15px;
                }
                @media #{$mobile} {
                  padding: 15px 20px;
                }
                label {
                  font-size: 12px;
                  font-weight: 500;
                  margin-bottom: 0;
                }
                input {
                  border: 0;
                  outline: none;
                  box-shadow: none;
                  padding-left: 0;
                  padding: 0;
                  height: auto;
                  font-weight: 500;
                  &::placeholder {
                    font-weight: normal;
                    color: #8d8b92;
                    @media (min-width: 1000px) and (max-width: 1399px) {
                      font-size: 14px;
                    }
                  }
                }
              }
              .custom-alert {
                margin-top: -12px;
                margin-bottom: 16px;
                font-size: 12px;
                line-height: 20px;
                color: #f04545;
                display: none;
              }
            }
            .send-invite {
              display: flex;
              justify-content: space-between;
              @media #{$mobile} {
                flex-direction: column;
              }
              .check-box {
                flex: 1;
                padding-left: 50px;
                @media (max-width: 1199px) {
                  padding-left: 40px;
                }
                input {
                  display: none;
                  &:checked + label:before {
                    background-color: #fd3e72;
                    border-color: #fd3e72;
                  }
                  &:checked + label:after {
                    border-color: #fff;
                  }
                }
                label {
                  position: relative;
                  font-size: 14px;
                  line-height: 20px;
                  color: #8d8b92;
                  max-width: 420px;
                  @media #{$mobile} {
                    margin-bottom: 40px;
                    font-size: 12px;
                  }
                  &:before {
                    content: "";
                    position: absolute;
                    width: 24px;
                    height: 24px;
                    top: 0;
                    left: -40px;
                    border-radius: 50%;
                    border: 1px solid #e0ded5;
                    text-align: center;
                    line-height: 24px;
                    transition: all 0.3s ease-in-out;
                    cursor: pointer;
                  }
                  &:after {
                    content: "";
                    display: block;
                    position: absolute;
                    top: 6px;
                    left: -31px;
                    width: 6px;
                    height: 10px;
                    border: solid #e0ded5;
                    border-width: 0 2px 2px 0;
                    transform: rotate(45deg);
                    cursor: pointer;
                    transition: all 0.3s ease-in-out;
                  }
                }
              }
              button {
                width: 140px;
                height: 40px;
                line-height: 40px;
                font-weight: 500;
                font-size: 14px;
                @media (min-width: 768px) and (max-width: 1199px) {
                  width: 120px;
                }
                @media #{$mobile} {
                  width: 100%;
                  min-height: 50px;
                }

                &:disabled,
                &.fade {
                  opacity: 0.5;
                }
              }
            }
          }
        }
        .bottom {
          border-top: 1px solid rgba(#e3e0d3, 0.4);
          padding-top: 30px;
          @media #{$betweentabdesk} {
            padding-top: 20px;
          }
          @media #{$mobile} {
            padding: 18px 0;
          }
          .no-invite {
            font-size: 14px;
            line-height: 20px;
            text-align: center;
            color: #8d8b92;
            display: none;
          }
          .invites-list {
            // display: none;
            li {
              display: flex;
              justify-content: space-between;
              align-items: center;
              @media #{$mobile} {
                display: block;
              }
              &:not(:last-child) {
                margin-bottom: 28px;
                @media #{$betweentabdesk} {
                  margin-bottom: 20px;
                }
              }
              .user-info {
                @media #{$mobile} {
                  margin-bottom: 15px;
                }
                h4 {
                  font-weight: 500;
                  font-size: 16px;
                  line-height: 20px;
                }
                p {
                  margin-top: 5px;
                  font-size: 14px;
                  line-height: 20px;
                  color: #8d8b92;
                }
              }
              span.status {
                background: rgba(#f0eee8, 0.5);
                border-radius: 3px;
                font-weight: bold;
                display: inline-block;
                width: 140px;
                height: 40px;
                line-height: 40px;
                text-align: center;
                @media #{$mobile} {
                  width: 100%;
                  display: block;
                }
                &.rewarded {
                  color: #62d5b4;
                  background: #e6f7ee;
                }
              }
            }
          }
        }
      }
    }
  }
}