.stepper {
  $root: &;
  --s-stepper-bullet: 38px;
  --s-stepper-bullet-half: calc( var(--s-stepper-bullet) / 2 );
  --step-transition: background .5s, color .5s;
  // all colors are define for the active state
  --step-content: url(../imgs/dashboard/check_red.svg);
  --step-color: rgba(#fff, 0.8);
  --step-bullet-color: #FD3E72;
  --step-bullet-bg: #fff;
  counter-reset: current-step;  
  position: relative;
  z-index: 1;
  border-radius: 10px 10px 0 0;
  font-weight: 500;
  font-size: 18px;
  @media (min-width:768px) and (max-width:1199px) {
    font-size: 16px;
    --s-stepper-bullet: 30px;
    --step-content: "\2714";
  }
  
  // don't know 
  &__input {
    counter-increment: steps;
    display: none;
    
  } 
  &__step {
    counter-increment: current-step;
    background: rgba(0, 0, 0, 0.05);
    display: flex;
    .stepper__button {
      opacity: 0.6;
      width: 100%;
      .arrow  {
        top: 27px;
        right: 20px;
        transform: rotate(-90deg);
        transition: all 0.3s ease-in-out;
        svg {
          path {
            stroke: #aaa;
          }
        }
      }
      
    }

    &.two {
      position: relative;
      &:after {
        content: "";
        position: absolute;
        top: 50%;
        right: 0;
        width: 1px;
        height: 70px;
        background-color: #E6E5E5;
        transform: translateY(-50%);
        @media (min-width:768px) and (max-width:1199px) {
          height: 50px;
        }
        @media #{$mobile} {
          top: auto;
          right: auto;
          left: 50%;
          transform: translate(-50% , 0);
          width: 90%;
          height: 1px;
          bottom: 0;
        }
      }
    }
    &.active {
      background-color: #F82A62;
      color: #fff;
      .arrow {
        svg {
          path {
            stroke: #fff;
          }
        }
      }
      &.two {
        &:after {
          display: none;
        }
      }
      .stepper__button {
        opacity: 1;
        .subtitle {
          opacity: 0.6;
        }
      }
    }
    &.darken {
      background-color: #ED1E56;
      .arrow {
        svg {
          path {
            stroke: #fff;
          }
        }
      }
    }
    
    // everything beyond the current step is deactivated    
    #{$root}__input:checked ~ & {
      --step-color: #5B5863;
      --step-bullet-bg: #D8D8D8;
      --step-bullet-color: #5B5863;
      --step-content: counter(current-step);
    }  
  
    // This is the current step
    #{$root}__input:checked + & {
      --step-bullet-bg: #fff;
      --step-bullet-color: #FD3E72;
      --step-color: #fff;
      .stepper__button {
        opacity: 1;
        background-color: #FD3E72;
        border-radius: 0 10px 0 0;
        @media #{$mobile} {
          border-radius: 0;
        }
        .text {
          .subtitle {
            opacity: 0.6;
          }
        }
      }
    }
    
  }

  .stepper__input.collapsed +.stepper__step {
    .arrow  {
      transform: rotate(-90deg);
    }
  }
  .stepper__step {
    .arrow.active {
      transform: rotate(0);
      svg {
        path {
          stroke: #fff;
        }
      }
    }
  }

  .stepper__input:checked + .stepper__step.two {
    &:after {
      display: none;
    }
  }

  &__button {    
    position: relative;
    text-align: center;    
    color: var(--step-color);
    display: flex;
    flex-direction: row;
    align-items: center;
    cursor: pointer;
    margin-bottom: 0;
    padding: 20px 5px;
    border-bottom: 2px solid #fd3e72;
    @media (min-width:768px) and (max-width:1199px) {
      padding: 13px 0;
    }
    @media #{$mobile} {
      padding: 13px 5px;
      border-bottom: 0;
    }
    .text {
      text-align: left;
      .subtitle {
        font-weight: 500;
        font-size: 14px;
        line-height: 20px;
        margin-top: 3px;
        @media (min-width:768px) and (max-width:1199px) {
          font-size: 12px;
          margin-top: 0;
        }
        
      }
    }
    
    &::before {
      content: var(--step-content);
      display: flex;
      justify-content: center;
      align-items: center;
      margin: 0 15px;
      height: var(--s-stepper-bullet);
      width: var(--s-stepper-bullet);
      border-radius: var(--s-stepper-bullet);
      transition: var(--step-transition);
      background: var(--step-bullet-bg);
      color: var(--step-bullet-color);
      font-weight: bold;
      font-size: 20px;
      min-width: 38px;
      @media (min-width:768px) and (max-width:1199px) {
        font-size: 18px;
        min-width: 30px;
        margin: 0 8px;
      }
      @media #{$mobile} {
        content: counter(current-step);
      }
    }
  }
  
  &--flexbox {
    display: flex;
    overflow: hidden;
    @media #{$mobile} {
      display: none;
    }
    
    #{$root}__step {
      flex-grow: 1;
      flex-shrink: 0;
      flex-basis: 0;      
    }
  }
}

.step__header {
  display: none;
  @media #{$mobile} {
    display: block;
  }
}

.manual-setup {
  background: #FFFFFF;
  box-shadow: 0px 6px 18px rgba(0, 0, 0, 0.05);
  border-radius: 10px;
  margin-top: 40px;
  @media #{$maxTab} {
    margin-top: 0;
  }
  #tab-content {
    padding: 25px;
    @media #{$mobile} {
      padding: 0;
      border-radius: 10px;
      overflow: hidden;
      box-shadow: 0px 6px 18px rgba(0, 0, 0, 0.08);
      .tab-pane {
        padding: 24px 13px;
      }
    }
    h3 {
      font-size: 28px;
      line-height: 140%;
      text-align: center;
      margin-bottom: 22px;
      @media (min-width:768px) and (max-width:1199px) {
        font-size: 20px;
      }
      @media #{$mobile} {
        font-size: 18px;
      }
    }
    ul.list {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      align-items: center;
      .list-item {
        margin: 0 0 20px;
        width: 32%;
        text-align: left;
        position: relative;
        @media #{$mobile} {
          width: 100%;
          margin: 0 0 10px;
        }
        .custom-control-input:checked + label {
          border: 2px solid $primaryColor;
          background: rgba(253, 62, 114, 0.05);
          &:before {
            background-color: $primaryColor;
            border-color: $primaryColor;
          }
        }
        label {
          font-weight: 500;
          font-size: 18px;
          position: relative;
          margin-bottom: 0;
          padding: 17px 0 17px 70px;
          border: 2px solid #E1E1E1;
          display: block;
          border-radius: 5px;
          cursor: pointer;
          margin-bottom: 0;
          @media (max-width:1199px) {
            padding: 15px 0 15px 65px;
            font-size: 16px;
          }
          @media (min-width: 992px) and (max-width: 1300px) {
            font-size: 14px;
            padding-left: 60px;
          }
          &:before {
            content: "";
            position: absolute;
            background: #fff;
            border: 3px solid #efefef;
            width: 24px;
            height: 24px;
            left: 25px;
            border-radius: 50%;
            top: 50%;
            transform: translateY(-50%);
          }
          &:after {
            position: absolute;
            left: 35px;
            transform: translateY(-62%) rotate(45deg);
            top: 50%;
            content: '';
            display: block;
            width: 6px;
            height: 12px;
            border: solid #fff;
            border-width: 0 3px 3px 0;
          }
        }
      }
    }
    .step_btns {
      text-align: center;
      display: flex;
      align-items: center;
      justify-content: center;
      border-top: 1px solid #EFEFEF;
      padding-top: 20px;
      margin-top: 20px;
      button {
        width: 132px;
        height: 50px;
        line-height: 50px;
        min-height: 50px;
        margin: 0 10px;
        font-weight: 500;
        font-size: 14px;
        display: inline-flex;
        align-items: center;
        justify-content: center;
        padding: 0;
        &.white_btn {
          border: 1px solid $primaryColor;
          &:hover {
            svg {
              transform: translateX(-3px);
            }
          }
          svg {
            transition: all 0.3s ease-in-out;
            margin-right: 10px;
            margin-left: 0;
          }
        }
      }
    }
    #protocol {
      .weblock {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 15px;
        border-top: 1px dashed #F0E8E8;
        padding: 21px 21px 0;
        margin-top: 20px;
        @media #{$mobile} {
          align-items: flex-start;
          padding: 21px 0 0;
        }
        .weblock-title {
          font-weight: 500;
          font-size: 20px;
          line-height: 140%;
          margin-bottom: 5px;
        }
        .weblock-txt {
          font-size: 16px;
          line-height: 20px;
          opacity: 0.7;
          @media (max-width:1199px) {
            font-size: 14px;
            font-weight: 400;
          }
        }
        .weblock-checker {
          position: relative;
          float: right;
          height: 24px;

          input {
            -webkit-appearance: none;
            border-radius: 50px;
            display: inline-block;
            background: #afadb2;
            width: 54px;
            height: 24px;
            position: relative;
            cursor: pointer;
            transition: all 0.3s ease-in-out;

            &:checked {
              background: $primaryColor;

              &:after {
                content: "ON";
                left: 8px;
                right: inherit;
              }

              & + label {
                right: 4px;
              }
            }

            &:after {
              content: "OFF";
              color: #fff;
              font-size: 12px;
              position: absolute;
              top: 4px;
              right: 6px;
              transition: all 0.3s ease-in-out;
            }
          }

          label {
            cursor: pointer;
            position: absolute;
            top: 3px;
            width: 18px;
            height: 18px;
            border-radius: 50%;
            background: #fff;
            right: 32px;
            transition: all 0.3s ease-in-out;
          }
        }
      }
      .next_step_two {
        width: 132px;
        height: 50px;
        min-height: 50px;
        font-size: 16px;
        display: flex;
        justify-content: center;
        align-items: center;
        @media #{$mobile} {
          width: 100%;
        }
      }
    }
    #location {
      @media #{$mobile} {
        padding: 24px 0;
      }
      .continents {
        @media #{$mobile} {
          max-height: 350px;
          overflow: auto;
          padding: 0 15px;
        }
      }
      .continent-title {
        font-size: 16px;
        line-height: 140%;
        color: rgba(91, 88, 99, 0.6);
        margin-bottom: 12px;
        text-transform: uppercase;
        @media (min-width:768px) and (max-width:1199px) {
          font-size: 14px;
          margin-bottom: 8px;
        }
      }
      ul.list {
        justify-content: flex-start;
        padding-bottom: 0;
        .list-item {
          width: 24%;
          margin: 0 5px 16px;

          @media (min-width:1199px) and (max-width:1360px) {
            width: 23%;
          }
          @media (min-width:768px) and (max-width:1199px) {
            width: 31%;
          }
          @media #{$mobile} {
            width: 100%;
            margin: 0 0 10px;
          }
          label {
            display: flex;
            padding-left: 55px;
            font-size: 16px;
            @media (min-width:768px) and (max-width:1360px) {
              padding: 11px 0 11px 45px;
            }
            @media #{$mobile} {
              padding: 11px 24px 11px 55px;
            }
            &::before {
