            }
            &::after {
              left: 25px;
            }
          }
          img {
            margin-right: 12px;
            @media (min-width:1199px) and (max-width:1360px) {
              margin-right: 8px;
            }
          }
        }
      }
      .socks_continent {
        display: none;
      }
      
    }
    #configuration {
      > div.config_content:not(:first-child) {
        display: none;
      }
        .openvpn-settings {
          @media (min-width: 1200px) {
            width: 90%;
            margin: auto;
          }
        }
        .tcp-port-select,
        .udp-port-select {
          display: none;
          @media (max-width:1199px) {
            margin-bottom: 10px;
          }
        }
        .udp-version-select,.tcp-version-select {
          @media (max-width:1199px) {
            margin-bottom: 10px;
          }
        }
        select {
          background: #ffffff;
          border: 1px solid #e0ded5;
          box-sizing: border-box;
          border-radius: 5px;
          display: block;
          width: 100%;
        }
        .select2-container--default .select2-selection--single {
          height: 60px;
          line-height: 60px;
          @media (max-width:1199px) {
            height: 50px;
            line-height: 50px;
          }
        }
        .select2-selection__rendered {
          line-height: 60px;
          padding-left: 20px;
          @media (max-width:1199px) {
            line-height: 50px;
          }
        }

        .download-config,
        .show-crendentials {
          width: 100%;
          height: 60px;
          line-height: 60px;
          font-size: 16px;
          display: block;
          cursor: pointer;
          @media (min-width:1199px) and (max-width:1360px) {
