              background: #fd3e72;
              border-radius: 4px;
              width: 30px;
              height: 30px;
              line-height: 0;
              text-align: center;
              margin: 0 5px;
              padding: 0;
              border: 0;
              min-height: auto;
              @media #{$mobile} {
                margin: 0;
              }
              img {
                padding: 5px;
              }
            }
          }
        }
        .wireguard-devices {
          .content {
            background: #ffffff;
            border: 1px solid #efefef;
            border-radius: 5px;
          }
          .devices-text {
            font-size: 18px;
            font-weight: 500;
            line-height: 22px;
            margin-bottom: 15px;
          }
          .device {
            position: relative;
            margin: 0 16px;
            padding: 16px 0;
            cursor: pointer;
            &:not(:last-child) {
              border-bottom: 1px solid #efefef;
            }
            p {
              font-size: 16px;
              line-height: 20px;
            }
            &.active {
              border-bottom: 0;
              .arrow {
                span {
                  background-color: #5b5863;
                }
                span:first-of-type {
                  transform: rotate(-45deg);
                }
                span:last-of-type {
                  transform: rotate(45deg);
                }
              }
            }
            .arrow {
              right: 0;
            }
          }
          .device-details {
            background: #fafafa;
            box-shadow: inset 0px 3px 3px #f6f6f6;
            padding: 16px;
            display: none;
            .location {
              font-weight: 500;
              font-size: 14px;
              line-height: 20px;
              margin-bottom: 14px;
              span {
                float: right;
                font-weight: 400;
              }
            }
            .device-buttons {
              display: flex;
              justify-content: space-between;
              align-items: center;
              button {
                width: 48%;
                min-height: 40px;
                line-height: 40px;
                height: 40px;
                display: flex;
                justify-content: center;
                align-items: center;
                font-size: 16px;
                span {
                  margin-left: 10px;
                }
                &.device-delete {
                  border: 1px solid $primaryColor;
                  border-radius: 4px;
                  background-color: transparent;
                  span {
                    color: $primaryColor;
                  }
                }
              }
            }
          }
        }
        .white_btn {
          @media #{$mobile} {
            width: 100%;
          }
        }
    }
  }
}
