.cont-five {
  .portforwarding {
    &-info {
      padding: 22px 32px;
      margin-top: 40px;
      margin-bottom: 30px;
      display: flex;
      align-items: flex-start;
      @media #{$tablet} {
        margin-top: 0;
      }
      @media #{$mobile} {
        margin-top: 0;
        padding: 15px;
      }
      img {
        margin-right: 25px;
        @media #{$mobile} {
          margin-right: 15px;
          width: 32px;
        }
      }
      p {
        font-weight: 500;
        font-size: 16px;
        line-height: 26px;
        @media #{$mobile} {
          font-size: 14px;
          line-height: 23px;
        }
      }
    }
  }
  .box {
    margin-bottom: 30px;
    border-top: 10px solid $primaryColor;
    @media (max-width: 1200px) {
      margin-bottom: 0;
    }
    h2 {
      @media #{$mobile} {
        text-align: center;
      }
    }
    .portforwarding-protocol {
      padding: 40px 40px 0;
      @media #{$mobile} {
        padding: 20px 30px 20px;
        border-bottom: 1px solid rgba(#e3d9dc, 0.4);
      }
      .protocol-content {
        display: flex;
        align-items: center;
        margin-bottom: 20px;
        @media #{$mobile} {
          flex-direction: column;
          .text {
            font-size: 16px;
            line-height: 20px;
            text-align: center;
            color: #5b5863;
            opacity: 0.7;
            margin-bottom: 18px;
          }
        }
        .spinner-border {
          display: none;
          border-width: 3px;
          border: 3px solid $primaryColor;
          border-right-color: transparent;
          @media #{$mobile} {
            margin-top: 15px;
          }
        }
        .select-container {
          width: 265px;
          margin-right: 20px;
          height: 50px;
          line-height: 50px;
          @media #{$mobile} {
            margin-right: 0;
            width: 100%;
            height: 60px;
            line-height: 60px;
          }
          .select2-container--default .select2-selection--single {
            height: 50px;
            line-height: 50px;
            @media #{$mobile} {
              height: 60px;
              line-height: 60px;
            }
            .select2-selection__rendered {
              line-height: 50px;
              @media #{$tablet} {
                font-size: 16px;
                padding-left: 20px;
              }
              @media #{$mobile} {
                line-height: 60px;
                padding-left: 20px;
                font-size: 16px;
              }
            }
          }
        }
        .add-btn {
          width: 175px;
          display: inline-flex;
          align-items: center;
          font-size: 16px;
          text-align: center;
          justify-content: center;
          padding: 13px;
          &:disabled {
            opacity: 0.5;
            cursor: no-drop;
          }
          @media #{$mobile} {
            width: 100%;
            min-height: 50px;
            line-height: 50px;
            margin-top: 15px;
          }
          svg {
            margin-right: 10px;
            margin-left: unset;
          }
        }
      }
    }
    .portforwarding_limit {
      display: none;
      .content {
        background: #ffeaf0;
        border-radius: 3px;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 8px;
        color: $primaryColor;
        font-size: 14px;
        
        @media #{$mobile} {
          padding: 10px;
          align-items: flex-start;
        }
        svg {
          margin-right: 10px;
          @media #{$mobile} {
            min-width: 20px;
          }
        }
      }
    }
    .ports {
      padding: 10px 40px 20px;
      @media #{$mobile} {
        padding: 10px 20px 20px;
      }
      table {
        width: 100%;
        thead {
          tr {
            border-bottom: 1px solid rgba(#e3d9dc, 0.4);
            th {
              padding: 10px;
            }
          }
          @media #{$mobile} {
            display: none;
          }
        }
        tbody {
          tr:nth-child(odd) {
            background-color: #fafafa;
            @media #{$mobile} {
              background-color: #fff;
            }
          }
          td {
            padding: 10px;
            @media #{$mobile} {
              padding: 15px 10px;
            }
          }
          tr {
            @media #{$mobile} {
              &:not(:last-child) {
                border-bottom: 1px solid rgba(#e3d9dc, 0.4);
              }
              td:first-child {
                display: none;
              }
              td:nth-child(2) {
                font-size: 20px;
                line-height: 20px;
                .protocol {
                  font-size: 14px;
                  margin-top: 5px;
                  display: block;
                }
              }
            }
          }
          .delete {
            float: right;
            button {
              display: flex;
              align-items: center;
              justify-content: center;
              color: $primaryColor;
              border: 0;
              font-weight: 500;
              font-size: 14px;
              line-height: 17px;
              padding: 10px;
              border: 1px solid $primaryColor;
              border-radius: 5px;
              background-color: #fff;
              img {
                margin-right: 8px;
              }
              @media #{$mobile} {
                padding: 8px;
                img {
                  margin-right: 0;
                  width: 22px;
                }
                span {
                  display: none;
                }
              }
            }
          }
        }
      }
      .show_no_ports {
        font-size: 14px;
        line-height: 22px;
        opacity: 0.7;
        max-width: 320px;
        margin: auto;
        padding: 40px 0;
      }
    }
  }
  .portforwarding-instrusctions {
    border-top: 10px solid $primaryColor;
    text-align: center;
    margin-top: 40px;
    @media (max-width: 1199px) {
      margin-top: 0;
      margin-bottom: 30px;
    }

    h2 {
      font-size: 24px;
      line-height: 70px;
      height: 70px;
      border-bottom: 1px solid rgba(#e3d9dc, 0.4);
      padding: 0 20px;
      text-align: left;
    }
    .desc {
      font-size: 14px;
      line-height: 22px;
      text-align: left;
      color: #5b5863;
      opacity: 0.7;
      padding: 20px;
    }
    .instructions {
      h3 {
        font-weight: 500;
        font-size: 18px;
        line-height: 22px;
        text-align: center;
        color: #fd3e72;
        cursor: pointer;
        border-top: 1px solid rgba(#e3d9dc, 0.4);
        padding: 20px 0 30px 20px;
        text-align: left;
        svg {
          transition: all 0.3s ease;
          margin-left: 15px;
        }
        &.open {
          background: #fafafa;
          svg {
            transform: rotate(180deg);
          }
        }
      }
      .instructions-content {
        display: none;
        .body {
          padding: 25px;
          ul {
            li {
              display: flex;
              font-size: 12px;
              line-height: 18px;
              text-align: left;
              margin-bottom: 12px;
              &:last-child {
                margin-bottom: 28px;
              }
              > span {
                width: 14px;
                height: 14px;
                border-radius: 50%;
                line-height: 14px;
                background-color: #fd3e72;
                font-weight: 500;
                font-size: 9px;
                color: #ffffff;
                display: inline-block;
                text-align: center;
                margin-right: 10px;
                min-width: 14px;
                position: relative;
                top: 2px;
              }
            }
          }
          .note {
            font-size: 12px;
            line-height: 18px;
            margin-bottom: 8px;
            text-align: left;
            span.mainColor {
              font-weight: 500;
            }
          }
        }
      }
    }
  }
}
