.cont-two {
  .smartdns-info {
    padding: 22px 32px;
    margin-top: 40px;
    margin-bottom: 30px;
    display: flex;
    align-items: center;
    @media #{$tablet} {
      margin-top: 0;
    }
    @media #{$mobile} {
      margin-top: 0;
      padding: 12px;
      align-items: flex-start;
    }
    img {
      margin-right: 25px;
      @media #{$mobile} {
        margin-right: 10px;
        margin-right: 10px;
        width: 20px;
      }
    }
    p {
      font-weight: 500;
      font-size: 16px;
      line-height: 26px;
      @media #{$mobile} {
        font-size: 12px;
        line-height: 23px;
      }
    }
  }
  .smartdns-status {
    border-top: 10px solid $primaryColor;
    padding-bottom: 20px;
    margin-bottom: 30px;
    &.one {
      .status {
        padding: 0;
        h2 {
          line-height: 70px;
          padding: 0 20px;
          font-size: 24px;
          @media (min-width: 1400px) {
            padding: 0 30px;
          }
        }
      }
      .content {
        padding: 0 20px 20px;
        @media (min-width: 1400px) {
          padding: 0 30px 20px;
        }
        @media #{$mobile} {
          padding: 0 14px;
        }
      }
      .whitelist-ip {
        p {
          font-size: 16px;
          line-height: 26px;
          margin: 20px 0;
          text-align: left;
          @media #{$mobile} {
            font-size: 14px;
          }
        }
      }
    }
    .form-ip,.form-ip2 {
      .input-field {
        position: relative;
      }
      .form-group {
        height: 80px;
        background: #ffffff;
        border: 1px solid #e0ded5;
        border-radius: 5px;
        padding: 16px;
      }
      label {
        font-weight: 500;
        font-size: 12px;
        line-height: 20px;
        margin-bottom: 0;
      }
      input {
        display: block;
        border: 0;
        width: 100%;
        font-weight: 500;
        &::placeholder {
          font-size: 16px;
          line-height: 20px;
          color: #8d8b92;
          font-weight: normal;
        }
      }
      .auto-detect-ip {
        background: #ffffff;
        border: 1px solid $primaryColor;
        border-radius: 5px;
        font-weight: 500;
        font-size: 16px;
        text-align: center;
        color: $primaryColor;
        width: 204px;
        height: 50px;
        line-height: 50px;
        position: absolute;
        right: 15px;
        top: 50%;
        transform: translateY(-50%);
        @media #{$mobile} {
          font-size: 14px;
          position: static;
          transform: translateY(0);
          width: 100%;
          margin-top: 15px;
        }
      }
      .save-ip {
        display: block;
        width: 233px;
        height: 56px;
        margin: 30px auto 0;
        font-size: 18px;
        @media #{$mobile} {
          width: 100%;
          height: 50px;
          line-height: 50px;
          min-height: 50px;
          margin: 20px auto 0;
        }
      }
    }
    .error {
      position: absolute;
      bottom: -23px;
      left: 15px;
      font-size: 12px;
      line-height: 20px;
      color: #f04545;
      display: none;
      @media #{$mobile} {
        position: relative;
        bottom: -2px;
        text-align: left;
      }

      svg {
        margin-right: 5px;
        position: relative;
        top: -1px;
      }
    }
    &.two, &.pending {
      display: none;
    }
    .spinner-border {
      color: #FE7E56;
      width: 1.5rem;
      height: 1.5rem;
      border-width: 2px;
    }
    .pending-content {
      padding: 25px;
      @media #{$mobile} {
        padding: 25px 10px;
      }
      h3 {
        font-size: 24px;
        line-height: 140%;
      }
      h4 {
        font-size: 18px;
        line-height: 140%;
      }
      p {
        font-size: 14px;
        line-height: 16px;
        margin-top: 12px;
        color: #8D8B92;
      }
    }

    .status {
      padding-left: 40px;
      border-bottom: 1px solid rgba(227, 224, 211, 0.4);
      height: 70px;
      line-height: 70px;
      @media #{$mobile} {
        padding-left: 16px;
      }
      &.pending {
        p {
          background: #fff0ec;
          color: #FE7E56;
        span.bull {
          background: #FE7E56;
        }
        }
      }
      p {
        font-weight: 500;
        font-size: 18px;
        line-height: 20px;
        color: #089b70;
        background: #ceefe5;
        border-radius: 5px;
        padding: 9px 18px;
        display: inline-flex;
        align-items: center;
        
      }
      span.bull {
        display: inline-block;
        width: 10px;
        height: 10px;
        border-radius: 50%;
        background: #08ae7e;
        margin-right: 6px;
      }
    }
    .activated-ip {
      padding: 13px 0 27px;
      margin: 0 40px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      @media #{$mobile} {
        flex-direction: column;
        justify-content: flex-start;
        align-items: flex-start;
        margin: 0 16px;
      }
      p {
        font-size: 15px;
        line-height: 20px;
        color: #8d8b92;
        max-width: 235px;
        @media #{$mobile} {
          margin-bottom: 18px;
        }
      }
      .ip {
        font-weight: 500;
        font-size: 18px;
        line-height: 20px;
        color: #5a5761;
        border: 1px solid #fd3e72;
        padding: 8px 15px;
        border-radius: 5px;
        display: inline-block;
      }
    }
    .smartdns-status-btns {
      display: flex;
      justify-content: center;
      align-items: center;
      @media #{$mobile} {
        flex-direction: column;
        align-items: flex-start;
        padding: 0 16px;
      }
      button {
        width: 180px;
        height: 50px;
        font-size: 16px;
        line-height: 50px;
        min-height: 50px;
        @media #{$mobile} {
          width: 100%;
        }
        &:first-child {
          margin-right: 30px;
          @media #{$mobile} {
            margin-right: 0;
            margin-bottom: 18px;
          }
        }
      }
    }
    .modal-dialog {
      text-align: center;
      @media (min-width: 767px) {
        max-width: 565px;
      }
      .modal-title {
        font-size: 24px;
        line-height: 44px;
        padding: 13px;
        border-bottom: 1px solid rgba(#E3E0D3,0.4);
        
        @media #{$mobile} {
          font-size: 20px;
          padding: 9px;
        }
      }
      .modal-subscription-body {
        padding: 18px 40px 45px;
        @media #{$mobile} {
          padding: 16px;
        }
        .change-ip-content {
          p {
            font-size: 14px;
            line-height: 20px;
            color: #5A5761;
            opacity: 0.7;
            max-width: 470px;
            margin: 0 auto 16px;
            @media #{$mobile} {
              &.desc {
                padding: 0 10px;
              }
            }
          }
          .old-ip {
            .ip {
              font-weight: 500;
              font-size: 20px;
              line-height: 20px;
              color: $primaryColor;
            }
          }
          .new-ip {
            h3 {
              font-weight: 500;
              font-size: 18px;
              line-height: 20px;
              color: #5A5761;
              margin: 30px 0 16px;
              @media #{$mobile} {
                margin: 25px 0 12px;
              }
            }
            .save-ip2 {
              width: 100%;
              height: 56px;
              margin: 30px auto 0;
              font-size: 18px;
              @media #{$mobile} {
                height: 50px;
                line-height: 50px;
                min-height: 50px;
              }
            }
          }
        }
      }
    }
  }
  .smartdns-servers {
    border-top: 10px solid $primaryColor;
    @media #{$mobile} {
      margin-bottom: 0;
    }

    &__title {
      padding: 10px 27px 18px;
      border-bottom: 1px solid rgba(#e3e0d3, 0.4);
      h3 {
        font-size: 24px;
        line-height: 44px;
      }
      p {
        font-size: 12px;
        line-height: 17px;
      }
    }
    &__list {
      padding: 0 27px 12px;
      @media (min-width: 1200px) and (max-width: 1400px) {
        padding: 0 15px 10px;
      }
      @media #{$mobile} {
        padding: 0 15px 5px;
      }
      li {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 18px 0;
        @media #{$mobile} {
          flex-direction: column;
          align-items: stretch;
          padding: 20px 15px;
          cursor: pointer;
        }
        &:not(:last-child) {
          border-bottom: 1px solid rgba(#e3e0d3, 0.4);
        }
        .country {
          display: flex;
          align-items: center;
          flex: 1;
          position: relative;
          @media (min-width: 768px) {
            .arrow {
              display: none;
            }
          }
          @media #{$mobile} {
            .arrow {
              top: 0;
              right: 0;
            }
            &.active {
              .arrow span:first-of-type {
                transform: rotate(-45deg);
              }
              .arrow span:last-of-type {
                transform: rotate(45deg);
              }
              .arrow span {
                background-color: #5b5863;
              }
            }
          }
          img {
            margin-right: 16px;
            @media (min-width: 1200px) and (max-width: 1400px) {
              margin-right: 8px;
            }
          }
          span {
            font-size: 14px;
            line-height: 20px;
            color: #5a5761;
          }
        }
        .country-ip {
          display: flex;
          align-items: center;
          @media #{$mobile} {
            justify-content: flex-end;
            display: none;
            margin-top: 20px;
          }
          span {
            display: inline-block;
            @media #{$tablet} {
              float: right;
            }
          }
          .ip {
            background: #f8f8f8;
            border-radius: 5px;
            font-size: 15px;
            line-height: 40px;
            text-align: center;
            color: #5a5761 !important;
            margin-right: 10px;
            font-weight: 500;
            width: 178px;
            border: 0;
            height: 40px;
            @media (min-width: 1200px) and (max-width: 1400px) {
              width: 120px;
            }
            @media #{$tablet} {
              width: auto;
            }
            @media #{$mobile} {
              width: 78%;
              font-size: 16px;
              padding: 10px 0;
            }
          }
        }
      }
    }
  }
  .smartdns-tutorials {
    border-top: 10px solid $primaryColor;
    margin-top: 40px;
    overflow: hidden;
    padding-bottom: 10px;
    @media (max-width: 1199px) {
      margin-top: 0;
      margin-bottom: 30px;
    }
    h2 {
      font-size: 24px;
      line-height: 70px;
      padding-left: 20px;
      height: 70px;
      border-bottom: 1px solid rgba(227, 224, 211, 0.4);
    }
    .accordion {
      .device {
        position: relative;
        &:not(:last-child) {
          &:after {
            content: "";
            position: absolute;
            height: 1px;
            background: rgba(#e3e0d3, 0.3);
            width: 90%;
            bottom: 0;
            left: 50%;
            transform: translateX(-50%);
          }
        }
        .device-header {
          > div {
            display: flex;
            align-items: center;
            justify-content: space-between;
            cursor: pointer;
            padding: 20px;
            background-color: #fafafa;
            @media #{$mobile} {
              padding: 16px 20px;
            }
            .arrow-down {
              transition: all 0.3s ease-in-out;
              transform: rotate(180deg);
              margin-right: 10px;
            }
            &.collapsed {
              background-color: #fff;
              .arrow-down {
                transform: rotate(0);
              }
            }
            .device-info {
              display: flex;
              align-items: center;
              img {
                margin-right: 20px;
              }
              .text {
                h3 {
                  font-weight: 500;
                  font-size: 16px;
                  line-height: 20px;
                  color: #5a5761;
                  @media #{$mobile} {
                    margin-bottom: 5px;
                  }
                }
                p {
                  font-size: 12px;
                  line-height: 16px;
                  color: #8d8b92;
                  padding-right: 30px;
                }
              }
            }
          }
        }
        .tuts_title {
          font-size: 14px;
          line-height: 140%;
          padding: 15px 24px 0;
        }
        .device-body {
          padding: 15px 24px 20px;
          counter-reset: plusOne;
          li {
            display: flex;
            font-size: 12px;
            line-height: 18px;
            &:not(:last-child) {
              margin-bottom: 12px;
            }
            &::before {
              counter-increment: plusOne;
              content: counter(plusOne);
              width: 14px;
              height: 14px;
              border-radius: 50%;
              line-height: 14px;
              background-color: #fd3e72;
              font-weight: 500;
              font-size: 9px;
              color: #ffffff;
              display: inline-block;
              text-align: center;
              margin-right: 10px;
              min-width: 14px;
              position: relative;
              top: 2px;
            }
            a {
              font-weight: 500;
              font-size: 12px;
              line-height: 18px;
              color: #fd3e72;
            }
          }
        }
      }
    }
  }
}