.alert-monitor {
  .alert-emails {
    border-top: 10px solid #fd3e72;
    margin-top: 30px;
    .box-title {
      padding: 5px 30px;
      @media #{$mobile} {
        padding: 5px 16px;
        height: auto;
        text-align: left;
      }
      h2,p {
        line-height: normal;
      }
      p {
        margin-top: 5px;
        font-size: 16px;
        opacity: 0.9;
      }
    }
    
  }
  .alert-emails-content {
    padding: 24px 30px;
    @media #{$mobile} {
      padding: 20px 16px 70px;
    }
    
    .row {
      > div {
        display: flex;
      }
    }
    .owl-carousel.owl-drag .owl-item,
    .owl-carousel .owl-stage {
      display: flex;
    }
    .owl-theme .owl-dots .owl-dot span {
      background: #c4c4c4;
      width: 15px;
      height: 15px;
      transition: all 0.3s ease-in-out;
    }
    .owl-theme .owl-dots .owl-dot.active span,
    .owl-theme .owl-dots .owl-dot:hover span {
      background-color: $primaryColor;
    }
    .owl-nav {
      margin-top: 0;
      .owl-next,
      .owl-prev {
        background: #fd3e72;
        border-radius: 5px;
        color: #fff;
        padding: 8px 20px !important;
        width: 100px;
        bottom: 0;
        margin: 0;
        @media #{$mobile} {
          padding: 6px !important;
          width: 80px;
          font-size: 14px;
          bottom: -50px;
        }
        &.disabled {
          background: #e7e7e7;
        }
      }
      .owl-prev {
        position: absolute;
        left: 0;
      }
      .owl-next {
        position: absolute;
        right: 0;
      }
    }
    .owl-dots {
      position: static;
      margin-top: 30px;
    }
    .owl-carousel .owl-item img {
      width: auto;
    }
    .new-email {
      background: linear-gradient(113.13deg, #fe7e56 -13.45%, #fd3e72 43.94%, #49a8ff 153.79%);
      border: 1px solid #f1f1f1;
      border-radius: 8px;
      width: 100%;
      cursor: pointer;
      padding: 17px 0;
      margin-bottom: 15px;
      .text {
        margin: auto;
        text-align: center;
        display: flex;
        align-items: center;
        img {
          width: 28px;
        }
        p {
          font-weight: 500;
          font-size: 16px;
          line-height: 24px;
          text-align: center;
          color: #ffffff;
          opacity: 1;
          @media #{$maxTab} {
          }
        }
      }
    }
    .email {
      width: 100%;
      background: #ffffff;
      border: 1px solid #f4f4f4;
      border-radius: 8px;
      overflow: hidden;
      transition: all 0.3s ease-in-out;
      min-width: 230px;
      &.active {
        border-color: $primaryColor;
        box-shadow: 0px 3px 14px rgba(0, 0, 0, 0.12);
      }
    }
    p {
      font-size: 12px;
      line-height: 16px;
      opacity: 0.6;
    }
    .email-top {
      padding: 11px;
      background: #f6f6f6;
      border-radius: 8px 8px 0px 0px;
      height: 63%;
      cursor: pointer;
      .status {
        border-radius: 3px;
        text-transform: uppercase;
        color: #fff;
        font-weight: bold;
        font-size: 12px;
        line-height: 25px;

        &.monitoring {
          background: #08ae7e;
        }
        &.breaches {
          background: $primaryColor;
        }
        &.pending {
          background: #fe7e56;
        }
        &.scanning {
          background: #49a8ff;
        }
        .bull {
          width: 6px;
          height: 6px;
          display: inline-block;
          border-radius: 50%;
          background: #fff;
          transform: translateY(-1px);
        }
      }
      .email-info {
        padding: 15px 0 6px;
        h5 {
          font-size: 14px;
          line-height: 18px;
          margin-bottom: 5px;
        }
      }
    }
    .email-bottom {
      padding: 11px;
      .scan {
        h5 {
          font-weight: 500;
          font-size: 12px;
          line-height: 14px;
          margin-top: 5px;
        }
      }
      .remove {
        button {
          border: 1px solid $primaryColor;
          display: flex;
          font-weight: 500;
          font-size: 14px;
          line-height: 24px;
          padding: 6px 17px 6px 12px;
          align-items: center;
          &.remove-pending {
            width: 38px;
            height: 38px;
            padding: 0;
            line-height: 38px;
            justify-content: center;
          }
          img {
            margin-right: 7px;
          }
        }
      }
      .resend {
        background: #ffffff;
        border: 1px solid #fe7e56;
        border-radius: 5px;
        font-weight: 500;
        font-size: 12px;
        line-height: 24px;
        color: #fe7e56;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 6px 12px;
        img {
          margin-right: 8px;
        }
      }
    }
  }

  .your-data-msg {
    padding: 18px 34px;
    background: #e6f7ee;
    border-radius: 10px;
    margin: 20px 0;
    @media #{$mobile} {
      padding: 16px;
    }

    .icon {
      margin-right: 18px;
      @media #{$tablet} {
        margin-right: 10px;
        img {
          width: 35px;
        }
      }
      @media #{$mobile} {
        margin-right: 9px;
        img {
          width: 30px;
        }
      }
    }
    .text {
      h4 {
        font-weight: 500;
        font-size: 16px;
        line-height: 20px;
        color: #08ae7e;
        margin-bottom: 3px;
      }
      p {
        font-size: 14px;
        line-height: 20px;
        color: #08ae7e;
        opacity: 0.8;
      }
    }
    &.pending {
      background: #fff4f1;
      .text {
        h4 {
          color: #fe7e56;
        }
        p {
          color: #fe7e56;
          opacity: 0.9;
        }
      }
    }
    &.scanning {
      background: #eef7ff;
      .text {
        h4 {
          color: #49a8ff;
        }
        p {
          color: #49a8ff;
          opacity: 0.9;
        }
      }
    }
  }
  .breach {
    margin-bottom: 10px;
    box-shadow: 0px 6px 18px rgba(0, 0, 0, 0.05);
  }
  #addingNewEmail {
    .verify-your-email {
      display: none;
    }
    .modal-content {
      position: relative;
      .close {
        position: absolute;
        width: 40px;
        height: 40px;
        border-radius: 50%;
        border: 1px solid transparent;
        line-height: 12px;
        padding: 0 !important;
        opacity: 1;
        top: 15px;
        right: 15px;
        transition: all 0.3s ease-in-out;
        svg {
          transition: all 0.3s ease-in-out;
        }
        &:hover {
          border-color: #ccc;
          opacity: 0.7;
          svg {
            transform: rotate(180deg);
          }
        }
      }
      .modal-title {
        font-size: 24px;
        line-height: 44px;
        padding: 13px;
        border-bottom: 1px solid rgba(227, 224, 211, 0.4);
        text-align: center;
      }
      .modal-new-email-body {
        padding: 30px 40px 50px;
        @media #{$mobile} {
          padding: 30px 20px;
        }
        h6 {
          font-size: 18px;
          line-height: 20px;
          color: #5a5761;
        }
        p {
          font-size: 14px;
          line-height: 20px;
          color: #5a5761;
          opacity: 0.7;
          margin: 11px 0 25px;
          @media #{$mobile} {
            margin: 11px 0 20px;
          }
        }
        form {
          .form-group {
            height: 80px;
            background: #ffffff;
            border: 1px solid #e0ded5;
            border-radius: 5px;
            padding: 11px 30px;
            text-align: left;
          }
          label {
            font-size: 12px;
            line-height: 20px;
            color: #5b5863;
            margin-bottom: 4px;
          }
          input {
            display: block;
            border: 0;
            width: 100%;
            &::placeholder {
              font-size: 16px;
              line-height: 20px;
              color: #8d8b92;
              font-weight: normal;
            }
          }
          .submit {
            width: 233px;
            line-height: 56px;
            min-height: 56px;
            font-size: 18px;
            margin-top: 5px;
            @media #{$mobile} {
              width: 100%;
            }
          }
          .custom-alert {
            margin-top: -12px;
            margin-bottom: 16px;
            font-size: 12px;
            line-height: 20px;
            color: #f04545;
            display: none;
            text-align: left;
            img {
              position: relative;
              top: -1px;
            }
          }
        }
        hr {
          margin-bottom: 25px;
          border-top: 1px dashed rgba(#e3e0d3, 0.4);
          @media #{$mobile} {
            margin-bottom: 20px;
          }
        }
        .resend_verification {
          width: 244px;
          line-height: 50px;
          min-height: 50px;
          font-size: 18px;
          @media #{$mobile} {
            width: 100%;
          }
        }
      }
    }
  }

  .alert-carousel {
    margin-bottom: 10px;
    &.owl-carousel .owl-item img {
      width: auto;
    }
    .owl-stage-outer { 
      overflow: visible;
      }
      
      .owl-item {
        opacity: 0;
        transition: opacity 500ms;
      }
      .owl-item.active {
        opacity: 1;
      }
    .owl-dots {
      counter-reset: dots;
      padding: 20px 0;
      margin-bottom: 30px;
    }
    .owl-dot:before {
      counter-increment: dots;
      content: counter(dots);
    }
    &.owl-theme .owl-dots .owl-dot {
      margin: 0 5px;
      width: 44px;
      line-height: 44px;
      border-radius: 5px;
      font-weight: 500;
      font-size: 20px;
      transition: all 0.2s ease-in-out;
      &:hover {
        background-color: rgba($primaryColor, 0.5);
        color: #fff;
      }
      @media #{$mobile} {
        width: 40px;
        line-height: 40px;
      }
      &.active {
        color: #fff;
        background-color: $primaryColor;
      }
      span {
        display: none;
      }
    }
    .owl-nav {
      .owl-next {
        right: 0;
        position: absolute;
        top: 15px;
      }
      .owl-prev {
        left: 0;
        position: absolute;
        top: 15px;
      }
      .owl-next,
      .owl-prev {
        transition: all 0.2s ease-in-out;
        border: 1px solid #fd3e72;
        border-radius: 5px;
        line-height: 42px;
        color: #fd3e72;
        font-weight: 500;
        width: 140px;
        &:hover {
          background: #ebebeb;
        }
        @media #{$mobile} {
          background-color: $primaryColor;
          width: 40px;
          line-height: 40px;
          .arrow-svg {
            path {
              stroke: #fff;
            }
          }
        }
        &.disabled {
          color: #5b5863;
          border-color: #c8c8c8;
          cursor: not-allowed;
          &:hover {
            background: none;
          }
          @media #{$mobile} {
            background: none;
            opacity: 1;
          }
          .arrow-svg {
            path {
              stroke: rgba(#5b5863, 0.3);
            }
          }
        }
      }
    }
    &.owl-theme .owl-nav {
      position: absolute;
      width: 100%;
      margin: 0;
    }
    /* Feel free to change duration  */
    .animated {
      -webkit-animation-duration: 400 ms;
      animation-duration: 400 ms;
      -webkit-animation-fill-mode: both;
      animation-fill-mode: both;
    }
    /* .owl-animated-out - only for current item */
    /* This is very important class. Use z-index if you want move Out item above In item */
    .owl-animated-out {
      z-index: 1;
    }
    /* .owl-animated-in - only for upcoming item
    /* This is very important class. Use z-index if you want move In item above Out item */
    .owl-animated-in {
      z-index: 0;
    }
    /* .fadeOut is style taken from Animation.css and this is how it looks in owl.carousel.css:  */
    .fadeOut {
      -webkit-animation-name: fadeOut;
      animation-name: fadeOut;
    }
    @-webkit-keyframes fadeOut {
      0% {
        opacity: 1;
      }
      100% {
        opacity: 0;
      }
    }
    @keyframes fadeOut {
      0% {
        opacity: 1;
      }
      100% {
        opacity: 0;
      }
    }
  }
}

.bord-active {
  border-top: 10px solid #fd3e72;
}

.combo-list {
  display: flex;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
  padding: 16px 30px;
  position: relative;
  @media #{$mobile} {
    padding: 16px;
  }
}
.combo-list h4 {
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  margin-bottom: 6px;
  color: #5a5761;
  text-overflow: ellipsis;
  white-space: nowrap;
  width: 200px;
  overflow: hidden;
}
.combo-list h6 {
  font-size: 14px;
  margin-bottom: 6px;
  color: #5b5863;
}
.combo-list p {
  font-size: 12px;
  line-height: 14px;
  color: #5a5761;
  &.breach-type {
    display: flex;
    align-items: center;
    span {
      color: rgba(#5a5761, 0.7);
      margin-left: 4px;
    }
  }
  &.date {
    color: #5b5863;
  }
}

.combo-list-box2 {
  min-width: 220px;
  display: flex;
  align-items: center;
}
.combo-list-heading {
  background: linear-gradient(138.72deg, #fe7e56 -13.67%, #fd3e72 55.74%, #49a8ff 189.16%);
  width: 37px;
  text-align: center;
  color: #fff;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  border-radius: 3px;
  line-height: 37px;
  float: left;
}
.combo-list-inner {
  float: left;
  margin-left: 15px;
}
.trash-btn {
  font-size: 14px;
  padding: 9px 0;
  min-width: 110px;
  margin-right: 30px;
  @media (max-width: 1200px) {
    padding: 9px;
    min-width: unset;
    margin-right: 20px;
  }
  img {
    margin-right: 8px;
    @media (max-width: 1200px) {
      margin-right: 0;
    }
  }
}

.accordion .collapsed .arrow-down {
  transform: rotate(0deg);
}
.accordion .arrow-down {
  transition: all 0.3s ease-in-out;
  transform: rotate(180deg);
}
.combo-content {
  position: relative;
  display: flex;
  padding: 20px 30px 30px;
  border-top: 1px solid rgba(#e3e0d3, 0.4);
  @media #{$mobile} {
    padding: 16px;
    flex-direction: column;
  }
  &.info {
    padding-bottom: 5px;
    h3 {
      font-size: 18px;
      margin-bottom: 14px;
      font-weight: 500;
      @media #{$mobile} {
        font-size: 16px;
        margin-bottom: 12px;
      }
    }
  }
  &.personal-data {
    @media (max-width: 1200px) {
      padding-bottom: 10px;
    }
    label {
      opacity: 0.6;
      font-weight: normal;
    }
    @media #{$mobile} {
      background: #fbfbfb;
      box-shadow: 0px 6px 18px rgba(0, 0, 0, 0.05);
      .form-group {
        border: 0;
        padding: 0;
        padding-bottom: 10px;
        border-bottom: 1px solid #f4f4f4;
        margin-bottom: 10px;
        &.last {
          border: 0;
          padding: 0;
        }
      }
    }
  }
}

.breach-date {
  padding: 16px;
  border-top: 1px solid rgba(#e3e0d3, 0.4);
  h6 {
    font-size: 12px;
    margin-bottom: 7px;
  }
  p {
    font-size: 12px;
  }
}
.combo-content .combo-content-right,
.combo-content .combo-content-left {
  h3 {
    font-size: 18px;
    margin-bottom: 14px;
    font-weight: 500;
    @media #{$mobile} {
      font-size: 16px;
      margin-bottom: 12px;
    }
  }
  p {
    font-size: 14px;
    line-height: 20px;
    color: #5a5761;
    margin-bottom: 25px;
    max-width: 580px;
    @media #{$mobile} {
      margin-bottom: 18px;
    }
  }
}
.combo-content-left {
  width: 33%;
  padding-right: 40px;
  @media (max-width: 1200px) {
    width: 45%;
    padding-right: 30px;
  }
  @media #{$mobile} {
    width: 100%;
    margin-bottom: 10px;
  }
  .form-group {
    max-width: 310px;
    @media #{$mobile} {
      max-width: none;
    }
  }
  .showOrHide {
    cursor: pointer;
    display: inline-block;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 20px;
    .eye {
      display: none;
    }
  }
  .personal-info-list {
    li {
      font-size: 14px;
      padding: 12px 0;
      display: flex;
      align-items: center;
      &:not(:last-child) {
        border-bottom: 1px solid #ECECEC;
      }
      svg {
        margin-right: 10px;
      }
    }
  }
}
@media (max-width: 767px) {
  .combo-list-box6 .trash-btn {
    width: 100%;
    min-height: 50px;
    line-height: 50px;
    margin-top: 16px;
  }
}

.combo-content .form-group {
