}

.combo-content-right {
  width: 65%;
  border-left: 1px solid #ececec;
  padding-left: 50px;
  @media (max-width: 1200px) {
    width: 64%;
    padding-left: 35px;
  }
  @media #{$mobile} {
    width: 100%;
    padding: 16px 0 0;
    border-top: 1px dashed#ececec;
    border-left: 0;
  }
}

.custom-btn2 {
  font-size: 16px;
  max-width: 245px;
  display: inline-block;
  line-height: 50px;
  text-align: center;
  width: 100%;
  @media #{$mobile} {
    line-height: 50px;
