}
.archive-tab-content {
  & > div:not(:first-child) {
    display: none;
  }
}

.w-35 {
  width: 30%;
  @media #{$mobile} {
    width: auto;
  }
}
.archive-breaches {
  margin-top: 10px;
  .archived {
    padding: 9px 30px;
    border: 1px solid rgba(#e3e0d3, 0.4);
    cursor: pointer;
    @media #{$mobile} {
      padding: 9px 16px;
    }
    .archived-status {
      font-size: 12px;
      line-height: 12px;
      background: #ffedf2;
      border-radius: 3px;
      padding: 4px 10px;
    }
  }
}
.combo-list-box6 {
  min-width: 155px;
  justify-content: flex-end;
  @media (max-width: 1200px) {
    min-width: unset;
  }
}

.alert-settings {
  color: #5a5761;
  .alert-emails-settings {
    .alert-emails-settings-cont {
      padding: 25px 30px;
      @media #{$mobile} {
        padding: 20px 16px;
      }
    }
    .breach-reports {
      @media #{$mobile} {
        margin-bottom: 15px;
      }
    }
    .divider {
      margin: 25px 0;
      border-top: 1px dashed #ececec;
    }
    .select-container {
      @media #{$mobile} {
        margin-top: 15px;
      }
    }
    .breach-frequency {
      @media #{$mobile} {
        flex-direction: column;
      }
      .select2-container--default .select2-selection--single .select2-selection__rendered {
        min-width: 290px;
        padding-left: 20px;
        @media #{$mobile} {
          min-width: unset;
          line-height: 50px;
        }
      }
      .select2-container--default .select2-selection--single {
        @media #{$mobile} {
          line-height: 50px;
          height: 50px;
        }
      }
    }
  }
  .remove-data {
    .remove-data-content {
      padding: 25px 30px;
      @media #{$mobile} {
        padding: 20px 16px;
      }
    }
    
    .remove-btn {
      padding: 12px 34px;
      font-size: 18px;
      @media (min-width: 768px) {
        min-width: 210px;
      }
      @media #{$mobile} {
        min-height: 50px;
        line-height: 50px;
        margin-top: 30px;
        width: 100%;
      }
    }
  }
  h3 {
    font-size: 18px;
    line-height: 20px;

    @media #{$mobile} {
      font-size: 16px;
      padding-right: 30px;
    }
  }
  p.text {
    font-size: 14px;
    line-height: 20px;
    max-width: 755px;
    margin-top: 11px;
    opacity: 0.7;
    padding-right: 30px;
  }
}

.powered-by {
  float: right;
  padding: 10px 15px;
  font-size: 12px;
  opacity: 0.7;
  a {
    color: $primaryColor;
  }
}

.mt-30 {
  margin-top: 30px;
}

.archive-passwords,
.archive-ip-address {
  .your-data-msg {
    margin-bottom: 10px;
  }
}

.archive-passwords-content,
.archive-ip-address-content {
  .right {
    border-left: 1px dashed #ececec;
    padding-left: 20px;
    @media #{$mobile} {
      border-left: 0;
      padding-left: 0;
      margin-top: 20px;
    }
  }
  .archive-passwords-header,
  .archive-ip-address-header {
    background: #ffffff;
    box-shadow: 0px 6px 18px rgba(0, 0, 0, 0.05);
    border-radius: 10px;
    padding: 10px 40px;
    margin-bottom: 10px;
    @media #{$mobile} {
      display: none;
    }
    h4 {
      font-size: 16px;
      line-height: 12px;
      padding: 10px 0;
    }
  }
  .archive-passwords-sites,
  .archive-ip-address-sites {
    .box {
      background: #ffffff;
      box-shadow: 0px 6px 18px rgba(0, 0, 0, 0.05);
      border-radius: 10px;
      padding: 20px 30px;
      margin-bottom: 10px;
      @media #{$mobile} {
        padding: 16px;
        overflow: hidden;
        .padding-0 {
          padding: 0;
        }
      }
      .ip {
        @media #{$mobile} {
          margin-top: 15px;
        }
      }
      .ip,
      .country {
        display: flex;
        justify-content: space-between;
        @media #{$mobile} {
          background: #fafafa;
          padding: 15px;
        }
        .text {
          font-size: 14px;
          line-height: 14px;
          opacity: 0.6;
        }
        .value {
          font-size: 16px;
          color: #5a5761;
        }
      }
      .ip,
      .country {
        font-size: 18px;
        color: #5a5761;
      }
      .country {
        span:first-child {
          margin-right: 10px;
        }
      }
    }
  }
  .archive-ip-address-sites {
    .box {
      @media #{$mobile} {
        padding: 16px 16px 0;
      }
    }
  }
  .website-info {
    .website-letter {
      background: linear-gradient(138.72deg, #fe7e56 -13.67%, #fd3e72 55.74%, #49a8ff 189.16%);
      width: 37px;
      text-align: center;
      color: #fff;
      border-radius: 3px;
      line-height: 37px;
      margin-right: 15px;
    }
    .password-inner {
      h4 {
        font-weight: 500;
        font-size: 16px;
        line-height: 20px;
        margin-bottom: 6px;
        color: #5a5761;
      }
      p {
        display: flex;
        align-items: center;
        font-size: 12px;
        line-height: 14px;
        color: #5a5761;
        span {
          opacity: 0.7;
          margin-left: 4px;
        }
      }
    }
  }
  .form-group {
    position: relative;
    border: 1px solid #dfdfdf;
    border-radius: 5px;
    padding: 5px 20px;
    margin-bottom: 0;
    &:not(:last-of-type) {
      margin-bottom: 15px;
    }
    input {
      width: 100%;
      background: none;
      border: 0;
    }
    .showOrHide {
      position: absolute;
      right: 20px;
      top: 50%;
      transform: translateY(-50%);
      cursor: pointer;
      .eye {
        display: none;
      }
    }
  }
}

.email-status-content {
  > div:not(:first-child) {
    display: none;
  }
}

.archive-personal-data {
  .box-style {
    margin-bottom: 10px;
    .combo-content {
      display: block;
    }
  }
}
