.dashboard-wrapper {
  background: #fafafa;
  @media (min-width: 992px) {
    display: flex;

    height: 100vh;
    width: 100%;
    overflow: hidden;
  }
}

.checker {
  position: relative;
  float: right;
  height: 24px;

  input {
    -webkit-appearance: none;
    border-radius: 50px;
    display: inline-block;
    background: #afadb2;
    width: 54px;
    height: 24px;
    position: relative;
    cursor: pointer;
    transition: all 0.3s ease-in-out;

    &:checked {
      background: #08ae7e;

      &:after {
        content: "ON";
        left: 7px;
        right: inherit;
      }

      & + label {
        right: 4px;
      }
    }

    &:after {
      content: "OFF";
      color: #fff;
      font-size: 12px;
      position: absolute;
      top: 4px;
      right: 5px;
      transition: all 0.3s ease-in-out;
    }
  }

  label {
    cursor: pointer;
    position: absolute;
    top: 3px;
    width: 18px;
    height: 18px;
    border-radius: 50%;
    background: #fff;
    right: 32px;
    transition: all 0.3s ease-in-out;
  }
}

.box-style {
  background: #ffffff;
  box-shadow: 0px 6px 18px #ece9e2;
  border-radius: 10px;
  overflow: hidden;
}

.box-title {
  height: 71px;
  line-height: 71px;
  padding: 0 30px;
  border-bottom: 1px solid rgba(#e3e0d3, 0.4);
  @media #{$mobile} {
    padding: 0 16px;
  }
  h2 {
    font-size: 24px;
    line-height: 71px;
  }
}

.scrollbar {
  &::-webkit-scrollbar {
    width: 10px;
    height: 10px;
  }
  &::-webkit-scrollbar-button {
    display: none;
  }
  &::-webkit-scrollbar-thumb {
    border-radius: 24px;
    border: 3px solid transparent;
    box-shadow: inset 0 0 10px 10px #e1e1e1;
  }
  &::-webkit-scrollbar-track {
    border: 3px solid transparent;
    box-shadow: inset 0 0 10px 10px #f9f9f8;
    @media #{$maxTab} {
      box-shadow: inset 0 0 10px 10px $primaryColor;
    }
  }
}
.scrollbar.aside-content {
  &::-webkit-scrollbar-track {
    border: 3px solid transparent;
    box-shadow: inset 0 0 10px 10px $primaryColor;
  }
  &::-webkit-scrollbar-thumb {
    box-shadow: inset 0 0 10px 10px #be1818;
  }
}

.select2-selection__arrow b {
  display: none;
}

.select2-dropdown {
  padding: 0;
  border-color: #e0ded5;
  border-radius: 0;
  border-top: 0;
  position: absolute;
}

.select2-container--default .select2-results > .select2-results__options {
  max-height: 287px;
  &::-webkit-scrollbar {
    width: 8px;
    height: 8px;
  }
  &::-webkit-scrollbar-button {
    display: none;
  }
  &::-webkit-scrollbar-thumb {
    background-color: rgba(0, 0, 0, 0.2);
  }
  &::-webkit-scrollbar-track {
    background-color: transparent;
  }
}

.select2-container--default {
  .select2-results__option--highlighted[aria-selected] {
    background: #f7f7f7;
    color: inherit;
  }
  .select2-search--dropdown .select2-search__field {
    border: 1px solid #e0ded5;
    padding-left: 20px;
    border-radius: 5px;
    display: none;
  }
  .select2-results__option[aria-selected="true"] {
    background: #fafafa;
  }
  .select2-results__option--highlighted[aria-selected] {
    background: #f7f7f7;
    color: inherit;
  }
  .select2-selection--single {
    height: 64px;
    line-height: 64px;
    border: 1px solid #e0ded5;
    border-radius: 5px;
    color: #5a5761;
    width: 100%;
    outline: none;
    .select2-selection__rendered {
      line-height: 64px;
      padding-left: 30px;
      font-weight: 500;
      font-size: 16px;
      color: #5a5761;
    }
    .select2-selection__placeholder {
      font-size: 14px;
      line-height: 20px;
      color: #8d8b92;
    }
  }
}

.select2-results__option {
  position: relative;
  height: 48px;
  line-height: 48px;
  padding: 0;
  padding-left: 20px;
  font-size: 14px;
  font-weight: 500;
  &:not(:last-child) {
    border-bottom: 1px solid #e0ded5;
  }
  span {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    display: flex;
    align-items: center;
    img {
      margin-right: 10px;
    }
  }
}
.select2-container--default.select2-container--open.select2-container--below .select2-selection--single {
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
}

aside {
  max-width: 400px;
  min-width: 300px;
  overflow: hidden;
  background-color: $primaryColor;
  position: relative;
  @media (min-width: 1400px) {
    min-width: 350px;
  }

  .aside-content {
    width: 100%;
    height: 100%;
    overflow: auto;
    padding-bottom: 70px;
    @media (min-width: 992px) {
      display: block !important;
    }
  }
  header {
    background-color: #f33a6c;
    padding: 14px 0 14px 30px;
    position: relative;
    z-index: 1;
    overflow: hidden;
    min-height: 64px;
    @media #{$betweentabdesk} {
      padding: 14px 0 14px 20px;
    }
    @media (min-width: 992px) {
      .navbar.dashboard_navbar {
        position: relative;
        padding: 0;
      }
      &:before {
        content: "";
        position: absolute;
        top: -117px;
        left: -61px;
        width: 168px;
        height: 168px;
        border-radius: 50%;
        background-color: #fb4c7c;
        display: block;
        z-index: 0;
      }
    }

    .navbar {
      @media (min-width: 992px) {
        position: relative;
        padding: 0;
      }
    }
    .logo {
      position: relative;
      z-index: 1;
      a {
        display: inline-block;
        @media #{$betweentabdesk} {
          img {
            width: 100px;
          }
        }
      }
    }
  }
  .dashboard-items,
  .account-items {
    h2 {
      padding-left: 30px;
      font-size: 16px;
      line-height: 26px;
      text-transform: uppercase;
      color: #ffffff;
      margin: 30px 0 20px;
      @media #{$betweentabdesk} {
        padding-left: 20px;
      }
      @media (max-width: 1400px) {
        margin: 15px 0;
      }
    }
    ul {
      @media #{$mobile} {
        padding: 0 14px 10px;
      }
    }
    li,
    .alert-item {
      padding: 10px 14px 10px 30px;
      font-weight: 500;
      font-size: 14px;
      line-height: 24px;
      display: flex;
      align-items: center;
      color: #fff;
      background: linear-gradient(180deg, #f93e71 0%, #fd3e72 100%);
      cursor: pointer;
      position: relative;
      @media #{$betweentabdesk} {
        padding: 10px 20px;
      }
      &.weblock-item {
        cursor:default;
      }
      .icon {
        display: flex;
        align-items: center;
        margin-right: 10px;
      }
      p .sub-text {
        display: block;
        opacity: 0.6;
        font-size: 12px;
        font-weight: normal;
      }

      .weblock-checker {
        position: absolute;
        height: 24px;
        right: 15px;
        top: 13px;

        @media #{$mobile} {
          right: 0;
        }

        input {
          -webkit-appearance: none;
          border-radius: 50px;
          display: inline-block;
          background: #DF1D52;
          width: 54px;
          height: 24px;
          position: relative;
          cursor: pointer;
          transition: all 0.3s ease-in-out;

          &:checked {
            background: #08AE7E;

            &:after {
              content: "ON";
              left: 8px;
              right: inherit;
            }

            & + label {
              right: 4px;
            }
          }

          &:after {
            content: "OFF";
            color: #fff;
            font-size: 12px;
            position: absolute;
            top: 0;
            right: 6px;
            transition: all 0.3s ease-in-out;
          }
        }

        label {
          cursor: pointer;
          position: absolute;
          top: 3px;
          width: 18px;
          height: 18px;
          border-radius: 50%;
          background: #fff;
          right: 32px;
          transition: all 0.3s ease-in-out;
        }
      }
      &:hover {
        @media (min-width: 992px) {
          background: #f2396b;
          box-shadow: inset 0px 3px 5px #eb3466;
        }
      }
      @media #{$mobile} {
        padding: 12px 16px;
        border-radius: 5px;
      }
      &.active {
        color: $primaryColor;
        background: #fafafa;
        box-shadow: 0px 4px 7px #e63767;

        svg {
          .rect-fill {
            fill: $primaryColor;
          }
          .rect-stroke {
            stroke: $primaryColor;
          }
          .path-fill {
            fill: $primaryColor;
          }
          .path-fill-w {
            fill: #fff;
          }
          .path-stroke {
            stroke: $primaryColor;
          }
          .path-stroke-w {
            stroke: #fff;
          }
        }
      }
    }
    .alert-item {
      @media #{$mobile} {
        padding: 9px 16px 9px 33px;
      }
    }
    .alert-item.selected {
      background: #fa3a6e;
    }
    .alert-arrow {
      transition: all 0.3s ease-in-out;
    }
    .alert-item.selected {
      .alert-arrow {
        transform: rotate(180deg);
      }
    }
    .alert-items {
      padding: 8px 5px 8px 45px;
      background: #f7356a;
      li {
        padding: 8px 0 8px 20px;
        border-radius: 5px;
        background: none;
        &.active {
          background: #fff;
        }
      }
    }
  }
  .sidebar-shape {
    position: absolute;
    right: 0;
  }
  @media #{$maxTab} {
    position: absolute;
    width: 100%;
    overflow: visible;
    z-index: 10;
    max-width: unset;
    min-width: unset;
    header {
      overflow: visible;
      padding: 0;
      z-index: 5;
      .navbar {
        background-color: #f33a6c !important;
        border-bottom: 0 !important;
        padding: 17px 20px;
        position: relative;
        > div {
          width: auto;
        }
        &:before {
          content: "";
          position: absolute;
          top: -112px;
          left: -45px;
          width: 168px;
          height: 168px;
          border-radius: 50%;
          background-color: #fb4c7c;
          display: block;
          z-index: 0;
        }
      }
      .navbar-toggler {
        border: 1px solid #fff;
        span {
          background-color: #fff !important;
        }
      }
    }
    .aside-content {
      background-color: $primaryColor;
      padding-bottom: 70px;
      position: fixed;
      transform: translateX(-100%);
      left: 0;
      top: 70px;
      bottom: 0;
      transition: all 0.15s ease-in;
      width: 330px;
      visibility: hidden;
      opacity: 0;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: stretch;
      &.show {
        transform: translateX(0);
        opacity: 1;
        visibility: visible;
      }
      .sidebar-shape {
        display: none;
      }
      .login {
        padding: 15px 25px 20px;
        .avatar-login {
          display: flex;
          align-items: center;
          border-bottom: 1px solid #f5f5f5;
          padding-bottom: 15px;
          margin-bottom: 15px;
          .avatar {
            width: 30px;
            margin-right: 15px;
          }
          p {
            font-weight: 500;
            font-size: 12px;
            line-height: 25px;
            .email {
              font-weight: 500;
              font-size: 16px;
              line-height: 18px;
              color: $primaryColor;
            }
          }
        }
        .sign-out-btn {
          border: 1px solid #fd3e72;
          border-radius: 5px;
          font-weight: 500;
          font-size: 14px;
          text-align: center;
          width: 100%;
          height: 50px;
          line-height: 50px;
          display: block;
          img {
            margin-right: 10px;
          }
        }
      }
    }
  }
}

.dashboard-main {
  flex: 1;
  padding-bottom: 50px;
  overflow: hidden;
  @media #{$maxTab} {
    padding: 70px 0 0;
  }
  .dashboard-container {
    @media (min-width: 1600px) {
      max-width: 1520px;
      margin: auto;
    }
  }
  .main-content {
    width: 100%;
    height: 100%;
    overflow: auto;
    .dashboard-content {
      > div {
        padding-bottom: 100px;
        @media #{$maxTab} {
          padding-bottom: 60px;
        }
      }
      > div:not(:first-child) {
        display: none;
      }
    }

    .announcement-box {
      background: linear-gradient(115deg, #fe6556 -12.43%, #fd3e72 55.94%, #49a8ff 152.76%);
      border-radius: 10px;
      padding: 5px;
      margin-top: 40px;
      @media #{$maxTab} {
        margin-bottom: 30px;
        margin-top: 0;
      }
      .anouncement-header {
        display: flex;
        align-items: center;
        justify-content: space-between;

        cursor: pointer;
        &.collapsed {
          .announcement-arrow {
            transform: rotate(180deg);
          }
        }
        > div {
          display: flex;
          align-items: center;
        }
        img {
          margin-right: 15px;
          @media #{$mobile} {
            margin-right: 5px;
            width: 30px;
          }
        }
        h4 {
          font-weight: 500;
          font-size: 22px;
          line-height: 44px;
          color: #ffffff;
          @media #{$mobile} {
            font-size: 18px;
            text-overflow: ellipsis;
            white-space: nowrap;
            overflow: hidden;
            width: auto;
          }
          @media (max-width: 450px) {
            width: 230px;
          }
        }
        .announcement-arrow {
          margin-right: 20px;
          transform: rotate(0);
          transition: all 0.2s ease-in-out;
        }
      }
      .card {
        margin-top: 5px;
        font-weight: normal;
        font-size: 16px;
        line-height: 20px;
        border-radius: 7px;
        color: #5b5863;
        @media #{$mobile} {
          padding: 14px;
          font-size: 14px;
        }
      }
    }
  }
  .dashboard-container {
    padding: 0 25px;
    @media #{$mobile} {
      padding: 0 15px;
    }
    @media (min-width: 1600px) {
      padding: 0 40px;
    }
  }
  header {
    @media (min-width: 992px) {
      padding: 12px 0;
      background: #ffffff;
      box-shadow: 0px 6px 18px rgba(0, 0, 0, 0.05);
    }

    .header-content {
      @media (min-width: 992px) {
        display: flex;
        justify-content: space-between;
      }
    }
    h2 {
      font-size: 26px;
      line-height: 36px;
      @media #{$betweentabdesk} {
        font-size: 22px;
      }
      @media #{$maxTab} {
        font-size: 28px;
        line-height: 36px;
        text-align: center;
        margin: 20px 0;
      }
    }
    .login {
      display: flex;
      align-items: center;
      .avatar {
        max-width: 40px;
      }
      p {
        font-weight: 500;
        font-size: 12px;
        margin-left: 22px;
        position: relative;
        border-right: 1px dashed #d1cdce;
        margin-right: 55px;
        padding-right: 50px;
        @media #{$betweentabdesk} {
          margin-left: 12px;
          margin-right: 25px;
          padding-right: 25px;
        }
        .email {
          font-weight: 500;
          font-size: 18px;
          line-height: 22px;
          color: #fd3e72;
          @media #{$betweentabdesk} {
            font-size: 16px;
          }
        }
      }
      .icons {
        display: flex;
        align-items: center;
        li:first-child {
          margin-right: 40px;
          @media #{$betweentabdesk} {
            margin-right: 20px;
          }
        }
      }
      @media #{$maxTab} {
        display: none;
      }
    }
  }
}
.warning-autorenewal {
  background: #ffecf1;
  border-radius: 10px;
  padding: 15px;
  display: flex;
  margin-bottom: 15px;
  svg {
    min-width: 26px;
  }
  p {
    font-size: 14px;
    line-height: 160%;
    color: #fd3e72;
    margin-left: 15px;
  }
}
