header.dashboard-page {
  .navbar {
    background-color: #fff;
  }
}

/* Start Dashboard */
.dashboard {
  background-color: $primaryColor;
  color: #fff;
  padding-top: 145px;

  @media #{$mobile} {
    padding-top: 114px;

    .container {
      padding: 0;
    }
  }

  @media #{$tablet} {
    .container {
      padding: 0;
      max-width: 100%;
    }
  }

  h2 {
    font-size: 56px;
    line-height: 56px;
    padding-bottom: 60px;
    margin-bottom: 0;

    @media #{$mobile} {
      font-size: 28px;
      line-height: 32px;
      padding-bottom: 28px;
    }

    @media #{$tablet} {
      font-size: 46px;
      line-height: 46px;
      padding-bottom: 45px;
    }
  }

  .dashboard-list {
    display: flex;
    justify-content: space-between;
    border-top: 1px solid rgba(#f9f8f4, 0.2);
    width: 100%;
    margin: auto;

    @media #{$maxTab} {
      overflow-x: auto;
      display: -webkit-box;
    }

    li {
      padding: 25px 13px;
      display: flex;
      align-items: center;
      position: relative;
      cursor: pointer;
      @media #{$mobile} {
        padding: 15px 20px 12px;
      }

      @media #{$tablet} {
        padding: 20px;
      }

      @media #{$betweentabdesk} {
        padding: 20px 7px;
      }

      &:before {
        content: "";
        display: block;
        width: 100%;
        height: 0;
        position: absolute;
        top: -1px;
        left: 0;
        background-color: #fff;
        visibility: hidden;
        opacity: 0;
        transition: all 0.3s ease-in-out;
      }
      .new {
        background-color: #08ae7e;
        border-radius: 3px;
        text-transform: uppercase;
        color: #fff;
        padding: 4px 6px;
        font-size: 10px;
        line-height: 10px;
        margin-left: 5px;
      }
      a {
        opacity: 0.7;
        transition: all 0.3s ease-in-out;
      }

      &.active,
      &:hover {
        a {
          opacity: 1;
        }

        &:before {
          opacity: 1;
          visibility: visible;
          height: 3px;
        }
      }

      &.active {
        svg {
          .active-item {
            visibility: visible;
            opacity: 1;
          }

          .inactive-item {
            visibility: hidden;
            opacity: 0;
          }
        }
      }

      a {
        color: #f9f8f4;
        font-weight: 500;
        display: block;
        position: relative;
        height: 100%;

        @media #{$mobile} {
          font-size: 14px;
        }

        @media #{$betweentabdesk} {
          font-size: 14px;
        }

        &:hover {
          &:before {
            opacity: 1;
            visibility: visible;
            height: 3px;
          }
        }

        svg {
          margin-right: 5px;
          position: relative;
          top: -2px;

          @media #{$mobile} {
            margin-right: 4px;
          }

          @media #{$tablet} {
            margin-right: 0;
          }
          @media #{$betweentabdesk} {
            margin-right: 0;
            width: 15px;
          }

          .active-item {
            visibility: hidden;
            opacity: 0;
            transition: all 0.3s ease-in-out;
          }

          .inactive-item {
            visibility: visible;
            opacity: 1;
            transition: all 0.3s ease-in-out;
          }
        }
      }
    }
  }
}

/* End Dashboard */

/* Start Download */
.download {
  margin-top: 60px;
  margin-bottom: 40px;

  @media #{$mobile} {
    margin-top: 20px;
    margin-bottom: 20px;
  }
  @media #{$betweentabdesk} {
    margin-top: 40px;
    margin-bottom: 20px;
  }

  .download-content {
    overflow: hidden;
    border-radius: 10px;
    background-color: #48464b;

    &.ios {
      background-image: url(../imgs/bg-down-dash-mob.png);
      background-size: auto;
      background-position: 65% 80%;
      position: relative;
      flex-direction: column;
      @media #{$tablet} {
        background-image: url(../imgs/bg-down-dash-tab.png);
        flex-direction: row-reverse;
      }
      &:after {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: rgba(#343239, 0.7);
        z-index: 1;
      }
      .item {
        position: relative;
        z-index: 2;
        width: 100%;
        flex-direction: column;
        padding: 20px 0;
        height: auto;
        overflow: hidden;

        @media #{$tablet} {
          width: 40%;
          flex-direction: row;
          padding: 0 50px 0 40px;
          justify-content: space-between;
        }
        &:first-child {
          border-bottom: 1px solid rgba(#e3e0d3, 0.2);
          @media #{$tablet} {
            border: 0;
            width: 60%;
          }
        }
        &:last-child {
          height: 125px;
          @media #{$tablet} {
            height: 132px;

            &:before {
              content: "";
              width: 1px;
              height: 50%;
              top: 50%;
              transform: translateY(-50%);
              position: absolute;
              right: 0;
              background-color: rgba(#e3e0d3, 0.2);
            }
          }
        }
        p {
          color: #fff;
          font-weight: bold;
          font-size: 14px;
          line-height: 17px;
          margin-bottom: 9px;
          @media #{$tablet} {
            margin-bottom: 0;
            text-align: left;
            width: 115px;
          }
        }
        a {
          padding: 0;
          height: auto;
          display: inline-block;
        }
        .device {
          position: absolute;
          top: 8px;
          left: 50%;
          transform: translateX(-50%);
          width: 80%;
        }
      }
    }

    &.android {
      .item {
        .device {
          top: 16px;
          display: block;
        }
        .android-btns {
          position: relative;
          .apk {
            color: #fff;
            text-decoration-line: underline !important;
            font-weight: 500;
            margin-left: 10px;
            svg {
              margin-right: 2px;
              top: -3px;
            }
            @media #{$tablet} {
              margin-left: 0;
              position: absolute;
              left: 50%;
              transform: translateX(-50%);
              bottom: -28px;
            }
          }
        }
      }
    }
    &.macos {
      .item {
        svg {
          position: relative;
          top: -4px;
        }
      }
    }

    .item {
      width: 50%;
      float: left;
      padding: 34px 40px;
      height: 132px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      > img {
        position: relative;
        top: 10px;
        @media #{$betweentabdesk} {
          top: 15px;
        }
      }

      &:first-child {
        position: relative;
        &:after {
          content: "";
          height: 50%;
          position: absolute;
          background-color: rgba(#e3e0d3, 0.2);
          width: 1px;
          right: 0;
          top: 50%;
          transform: translateY(-50%);
        }
      }

      @media #{$betweentabdesk} {
        height: 120px;
        padding: 30px;
      }

      p {
        display: inline-block;
        margin-bottom: 0;
        width: 120px;
        line-height: 20px;
        font-weight: 500;
        color: #fff;

        @media #{$maxTab} {
          width: 100%;
          display: block;
          text-align: center;
          margin-bottom: 5px;
          font-size: 12px;
        }

        // @media #{$tablet} {
        //   font-size: 15px;
        //   margin-bottom: 10px;
        // }
        // @media #{$mobile} {
        //   color: #5b5863;
        // }
      }

      a {
        padding: 20px 123px;
        height: 64px;
        font-size: 16px;

        // @media #{$maxTab} {
        //   display: block;
        //   padding: 14px 42px;

        //   width: 100%;
        //   height: 52px;
        // }

        // @media #{$mobile} {
        //   padding: 14px 0;
        //   text-align: center;
        // }

        @media #{$betweentabdesk} {
          padding: 20px 70px;
        }

        svg {
          margin-right: 10px;
          position: relative;
          top: -1px;
        }

        &.web-store {
          border: 1px solid #e3e0d3;
          background: none;
          color: #5b5863;
          transition: all 0.3s ease-in-out;
          border-radius: 5px;

          &:hover {
            opacity: 0.7;
          }

          // @media #{$mobile} {
          //   padding: 12px 42px;
          // }

          img {
            position: relative;
            top: -2px;
            left: -5px;
          }
        }
      }
    }
  }
  .download-content-chrome {
    background: #48464b;
    border: 1px solid #ebe8df;
    overflow: hidden;
    border-radius: 10px;

    .item {
      width: 50%;
      float: left;
      padding: 34px 60px;
      height: 132px;
      display: flex;
      justify-content: space-between;
      align-items: center;

      @media #{$betweentabdesk} {
        height: 120px;
        padding: 30px;
      }

      &:first-child {
        position: relative;
        &:after {
          content: "";
          height: 50%;
          position: absolute;
          background-color: rgba(#e3e0d3, 0.2);
          width: 1px;
          right: 0;
          top: 50%;
          transform: translateY(-50%);
        }
      }

      p {
        display: inline-block;
        margin-bottom: 0;
        width: 120px;
        color: #fff;
        font-weight: bold;
        font-size: 16px;
        line-height: 20px;
      }

      a {
        padding: 20px 46px;
        height: 64px;
        font-size: 16px;
        @media #{$betweentabdesk} {
          padding: 20px 30px;
        }

        svg {
          margin-right: 5px;
          position: relative;
          top: -2px;
        }

        &.web-store {
          padding: 0;

          &:hover {
            opacity: 0.7;
          }
        }
      }
    }
  }
}

/* End Download */

/* Start Subscribe */
.subscribe {
  padding-bottom: 40px;
  &__box {
    display: flex;
    @media #{$tablet} {
      &.pl-10 {
        padding-left: 10px;
      }
      &.pr-10 {
        padding-right: 10px;
      }
    }
  }

  @media (max-width: 1200px) {
    padding-bottom: 20px;
  }

  .right-column {
    transition: all 0.3s ease-in-out;
  }

  .left-column,
  .right-column {
    background-color: #fff;
    border-radius: 10px;
    width: 100%;
    overflow: hidden;

    .item-heading {
      h3 {
        font-size: 24px;
        line-height: 35px;
        position: relative;
        border-bottom: 1px solid rgba(#e3e0d3, 0.4);
        padding: 22px 40px;
        margin-bottom: 0;
        display: flex;
        justify-content: space-between;

        @media #{$mobile} {
          font-size: 18px;
          line-height: 18px;
          padding: 19px 13px;
        }

        @media #{$tablet} {
          font-size: 20px;
          line-height: 36px;
          padding: 20px;
        }
        @media #{$betweentabdesk} {
          padding: 22px 32px;
        }

        .settings {
          font-size: 12px;
          line-height: 24px;
          text-align: center;
          color: #8d8b92;
          border: 1px solid #e3e0d2;
          border-radius: 5px;
          display: inline-block;
          padding: 5px 12px;
          background: none;
          transition: all 0.3s ease-in-out;

          svg {
            position: relative;
            top: -1px;
            path {
              transition: all 0.3s ease-in-out;
            }
          }

          &:hover {
            color: #5b5863;

            svg {
              path {
                fill: #5b5863;
              }
            }
          }

          @media #{$mobile} {
            right: 13px;
            top: 12px;
            padding: 3px 7px;

            &:not(svg) {
              font-size: 0;
            }
          }

          @media #{$tablet} {
            right: 20px;
            top: 17px;
          }
        }
      }
    }

    .list {
      padding-left: 40px;
      padding-right: 40px;
      padding-bottom: 15px;
      @media #{$betweentabdesk} {
        padding-left: 32px;
        padding-right: 32px;
      }

      &.two {
        padding-top: 10px;
        padding-bottom: 20px;

        @media #{$betweentabdesk} {
          padding-top: 0;
        }
        @media #{$tablet} {
          padding-top: 0;
        }
        @media #{$mobile} {
          padding-top: 0;
          padding-bottom: 10px;
        }
      }

      @media #{$mobile} {
        padding: 0 13px;
      }

      @media #{$tablet} {
        padding: 0 20px;
      }

      li {
        padding-top: 30px;
        padding-bottom: 32px;
        position: relative;
        display: flex;
        justify-content: space-between;
        align-items: flex-start;

        .active {
          position: relative;
          display: inline-block;
          font-size: 14px;
          font-weight: 500;
          color: #08ae7e;
          background: #edf8f5;
          border-radius: 5px;
          margin-right: 3px;
          padding: 0 21px;
          line-height: 40px;
          @media #{$betweentabdesk} {
            padding: 0 16px 0 21px;
          }
          @media #{$maxTab} {
            padding: 0 18px;
            line-height: 30px;
            display: block;
            margin-bottom: 5px;
          }

          &:before {
            content: "";
            position: absolute;
            width: 6px;
            height: 6px;
            background-color: #08ae7e;
            border-radius: 50%;
            display: inline-block;
            top: 50%;
            transform: translateY(-50%);
            left: 12px;
            @media #{$maxTab} {
              left: 9px;
            }
          }
        }

        &.android,
        &.mac-os {
          position: relative;
          z-index: 1;

          &:hover {
            .download-menu {
              opacity: 1;
              visibility: visible;
              top: -3px;
            }
          }

          .download-menu {
            position: absolute;
            display: flex;
            align-items: center;
            z-index: 2;
            background: #ffffff;
            border: 1px solid #ebe8df;
            box-shadow: 0px 10px 16px rgba(135, 134, 131, 0.18);
            border-radius: 10px;
            opacity: 0;
            visibility: hidden;
            left: 0;
            right: 0;
            top: 10px;
            height: 70px;
            transition: all 0.3s ease-in-out;

            @media #{$mobile} {
              height: 60px;
            }

            a {
              color: #5a5761;
              position: relative;
              flex: 1;
              text-align: center;
              padding: 4px 0;

              @media #{$mobile} {
                font-size: 12px;
              }

              img {
                position: relative;
                top: -2px;
                left: 8px;
              }

              &.app-store,
              &.play-store {
                border-right: 1px solid #ebe8df;
              }

              &.play-store {
                img {
                  top: 0;
                  left: 3px;
                }
              }

              &.app-store {
                img {
                  left: 3px;
                }
              }
            }
          }
        }

        @media #{$mobile} {
          padding: 12px 0;
        }

        @media #{$tablet} {
          padding-bottom: 29px;
        }

        &:not(:last-child) {
          border-bottom: 1px dashed rgba(#e3e0d3, 0.7);
        }

        .list-item-text {
          label {
            font-size: 14px;
            line-height: 20px;
            color: #8d8b92;

            margin-bottom: 5px;

            @media #{$mobile} {
              font-size: 12px;
            }
          }

          p {
            font-size: 16px;
            line-height: 20px;
            color: #5a5761;
            font-weight: 500;
            margin-bottom: 5px;

            @media #{$mobile} {
              font-size: 14px;
            }

            @media #{$tablet} {
              font-size: 14px;
            }
          }
          .expire-soon {
            font-size: 14px;
            line-height: 20px;
            color: #f04545;
            font-weight: 500;
            display: inline-block;

            img {
              position: relative;
              top: -2px;
            }
          }
        }

        .auto-renewal {
          text-align: center;

          span:first-child {
            font-size: 12px;
            color: #8d8b92;
            display: block;
            margin-bottom: 3px;
          }
          .label {
            font-size: 12px;
            line-height: 23px;
            border: 1px solid #d0f3e9;
            box-sizing: border-box;
            border-radius: 5px;
            color: #08ae7e;
            display: inline-block;
            padding: 0 8px;
            font-weight: 500;
          }
        }

        .button {
          font-size: 14px;
          line-height: 40px;
          padding: 0 16px;
          display: inline-block;

          @media #{$maxTab} {
            min-height: 26px;
            line-height: 30px;
          }

          &:hover {
            color: #fff;
          }

          &.edit {
            padding: 0px 16px;
          }
        }

        .link {
          font-size: 14px;
          margin-top: 15px;

          display: inline-block;

          @media #{$mobile} {
            font-size: 12px;
            margin-top: 5px;
          }

          @media #{$tablet} {
            margin-bottom: 14px;
          }
        }
      }
    }
  }

  .right-column {
    @media #{$mobile} {
      margin-top: 12px;
    }

    .list {
      li {
        position: relative;
        padding-top: 17px;
        padding-bottom: 14px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        @media #{$betweentabdesk} {
          padding: 17px 0;
        }
        @media #{$tablet} {
          padding: 14px 0;
          flex-direction: column;
          align-items: flex-start;
        }
        @media #{$mobile} {
          padding: 8px 0;
          align-items: flex-start;
          flex-direction: column;
        }
        .item-info {
          display: flex;
          align-items: center;
          @media #{$maxTab} {
            margin-bottom: 10px;
          }
          .info {
            .name {
              margin-bottom: 0;
              font-size: 16px;
              line-height: 20px;
              color: #5a5761;
              font-weight: 500;
              display: inline-block;
            }
            .version {
              font-size: 14px;
              line-height: 16px;
              color: #33303f;
              opacity: 0.6;
              display: block;
              font-weight: 400;
              @media (max-width: 1199px) {
                font-size: 12px;
              }
            }
          }
        }

        &:hover {
          .icon {
            path {
              fill: #5b5863;
            }

            &.firefox {
              .line {
                stroke: #5b5863;
                fill: none;
              }
            }

            &.smart-tv {
              path {
                fill: none;
                stroke: #5b5863;
              }
            }
          }
        }

        &:not(:last-child) {
          border-bottom: 1px dashed rgba(#e3e0d3, 0.7);
        }

        .download-android {
          @media #{$maxTab} {
            display: flex;
            align-items: center;
          }
          @media #{$maxTab} {
            align-self: flex-end;
          }
        }
        .icon {
          width: 25px;
          margin-right: 10px;
          path {
            transition: all 0.3s ease-in-out;
          }
        }
        .apk {
          font-weight: 500;
          color: $primaryColor;
          font-size: 13px;
          transition: all 0.3s ease-in-out;
          svg {
            width: 13px;
            position: relative;
            top: -3px;
          }
          &:hover {
            color: $linkHoverColor;
          }
          @media #{$maxTab} {
            margin-right: 5px;
          }
        }

        .download-icon {
          font-size: 14px;
          line-height: 20px;
          color: #8d8b92;
          display: inline-block;
          transition: all 0.3s ease-in-out;
          @media #{$maxTab} {
            align-self: flex-end;
          }
          &:hover {
            opacity: 0.8;
          }
          &.macOs-btn {
            background-color: $primaryColor;
            color: #fff;
            padding: 10px 26px 8px;
            border-radius: 5px;
            transition: all 0.3s ease-in-out;
            &:hover {
              background-color: $linkHoverColor;
              opacity: 1;
            }
          }
          svg {
            position: relative;
            top: -3px;
            left: -2px;
            &.win-icon {
              top: -1px;
            }
            path {
              transition: all 0.3s ease-in-out;
            }
          }
        }
        .chrome-btn {
          background: #ffffff;
          border: 1px solid #e3e0d3;
          padding: 7px 17px;
          border-radius: 5px;
          font-weight: 500;
          font-size: 14px;
          @media #{$maxTab} {
            align-self: flex-end;
          }
          &.firefox {
            padding: 6px 19px;
          }
          img,
          svg {
            margin-right: 5px;
          }
          &:hover {
            color: #5b5863;
          }
        }
      }
    }
  }

  /* subscription settings */
  #subscriptionSettings {
    .modal-title {
      text-align: center;
      font-weight: bold;
      font-size: 24px;
      line-height: 24px;
      .close {
        top: 45px;
        right: 40px;
        @media #{$mobile} {
          top: 30px;
          right: 30px;
        }
      }
    }
    .modal-content {
      border-radius: 10px;
      border: 0;
      padding: 50px 40px 35px;
      @media #{$mobile} {
        padding: 90px 30px 35px;
      }
    }
    h3 {
      font-size: 24px;
      line-height: 44px;
      color: #8d8b92;
      border-bottom: 1px solid #e3e0d3;
      margin-bottom: 0;
      padding-bottom: 15px;
      font-weight: normal;
      margin-top: 25px;

      @media #{$mobile} {
        font-size: 18px;
        line-height: 18px;
        margin-bottom: 0;
        padding-bottom: 19px;
        margin-top: 30px;
      }
    }

    .list {
      margin-top: 10px;

      @media #{$mobile} {
        margin-top: 0;
      }

      li {
        position: relative;
        padding: 11px 0;

        @media #{$mobile} {
          padding: 20px 0;
        }

        &:not(:last-child) {
          border-bottom: 1px dashed #e3e0d3;
        }

        .list-item-text {
          &.first {
            display: flex;
            justify-content: space-between;
            align-items: center;
          }
          p {
            font-size: 16px;
            line-height: 20px;
            color: #5a5761;
            font-weight: 500;
            margin-bottom: 5px;

            @media #{$mobile} {
              font-size: 14px;
              margin-bottom: 0;
            }

            &.label {
              font-size: 14px;
              line-height: 20px;
              color: #8d8b92;
              font-weight: normal;

              @media #{$mobile} {
                font-size: 12px;
              }
            }
          }

          .status {
            border: 1px solid #d0f3e9;
            border-radius: 5px;
            font-size: 16px;
            line-height: 26px;
            color: #08ae7e;
            display: inline-block;
            padding: 0 11px;
            font-weight: 500;

            @media #{$mobile} {
              font-size: 14px;
            }
          }

          .menu-btn {
            width: 32px;
            height: 32px;
            border-radius: 50%;
            line-height: 10px;
            background: #fefefe;
            border: 1px solid #efeee6;
            transition: all 0.3s ease-in-out;

            @media #{$mobile} {
              border: 0;
              background: none;
            }

            svg {
              path {
                transition: all 0.3s ease-in-out;
                fill: #b7b5ba;
              }
            }

            &:hover {
              border-color: #bfbeba;

              svg {
                path {
                  fill: #9d9ba1;
                }
              }
            }
          }
        }

        .cancel {
          font-size: 14px;
          line-height: 20px;
          color: #5a5761;
          padding: 16px;
          background: #ffffff;
          box-shadow: 0px 20px 40px rgba(240, 238, 233, 0.8), 0px 3px 8px #e6e4df;
          position: absolute;
          right: 0;
          top: 75px;
          display: none;
          transition: all 0.2s ease-in-out;
          font-weight: 500;
          border: 0;
          z-index: 444;

          @media #{$mobile} {
            font-size: 12px;
            padding: 12px;
            right: 10px;
            z-index: 2;
          }
        }

        .button {
          position: absolute;
          top: 40px;
          right: 0;
          font-size: 14px;
          line-height: 40px;
          padding: 0px 13px;
          display: inline-block;

          @media #{$mobile} {
            top: 20px;
          }

          &:hover {
            color: #fff;
          }

          &.edit {
            padding: 0px 15px;
          }
        }

        .link {
          font-size: 14px;

          margin-top: 15px;
          display: inline-block;

          @media #{$mobile} {
            font-size: 12px;
            margin-top: 5px;
          }
        }
      }
    }

    .back-to-dashboard {
      text-align: right;
      border-radius: 0 0 10px 10px;

      @media #{$mobile} {
        padding: 10px;
      }

      @media #{$tablet} {
        padding: 30px 40px;
      }

      a {
        padding: 13px 41px;
        display: inline-block;

        @media #{$mobile} {
          width: 100%;
          display: block;
          padding: 16px 0;
          font-size: 16px;
          text-align: center;
        }

        &:hover {
          color: #fff;

          svg {
            left: -15px;
          }
        }

        svg {
          position: relative;
          transition: all 0.3s ease-in-out;
          left: -11px;
          top: 0;
        }
      }
    }
  }

  #cancelrenewal {
    .modal-dialog {
      @media (min-width: 1200px) {
        position: relative;
        left: -8px;
      }

      @media #{$mobile} {
        width: auto;
      }

      .modal-content {
        padding: 87px 70px;

        @media #{$tablet} {
          padding: 82px 40px;
        }
        @media #{$mobile} {
          text-align: center;
          padding: 30px;
        }
      }
      .close {
        top: 0;
        right: 0;

        @media #{$mobile} {
          top: 0;
          left: 50%;
          right: auto;
          transform: translateX(-50%);
        }
      }
      .creditModal-header {
        h3 {
          font-size: 24px;
          line-height: 24px;
          text-align: center;
          @media #{$mobile} {
            margin-top: 60px;
          }
        }
        h5 {
          font-size: 24px;
          line-height: 44px;
          border-bottom: 1px solid #e3e0d3;
          padding: 55px 0 15px;
          margin: 0;

          @media #{$mobile} {
            text-align: center !important;
            padding: 30px 0 25px;
            font-size: 18px;
            line-height: 18px;
            width: 100%;
          }
        }
      }

      .modal-body {
        width: 80%;
        padding: 35px 0 40px;

        @media #{$mobile} {
          padding: 23px 0;
          width: 95%;
        }

        p {
          font-size: 18px;
          line-height: 28px;
          color: #5b5863;
          margin-bottom: 0;

          @media #{$mobile} {
            font-size: 14px;
            line-height: 24px;
            width: 100%;
          }
        }
      }

      .cancel-btns {
        display: flex;
        flex-direction: row-reverse;
        @media #{$mobile} {
          flex-direction: column;
        }
        .confirm-cancellation,
        .keep-automatic-renewal {
          padding: 17px 36px;
          margin-left: 16px;

          @media #{$mobile} {
            width: 100%;
            display: block;
            font-size: 16px;
            margin-left: 0;
          }
        }

        .keep-automatic-renewal {
          color: $primaryColor;
          background-color: #ffecf1;
          padding: 17px 24px;
          @media #{$mobile} {
            margin-top: 12px;
          }
        }
      }
    }
  }

  /* subscription settings */
}

