    @media #{$mobile} {
      width: auto;
      margin: 100px 20px;
    }
  
    @media #{$tablet} {
      max-width: 700px;
    }
  
    .padd {
      padding-right: 10px;
      padding-left: 10px;
  
      @media #{$mobile} {
        padding-right: 15px;
        padding-left: 15px;
      }
    }
  
    .padd-right {
      padding-right: 10px;
  
      @media #{$mobile} {
        padding-right: 15px;
      }
    }
  
    .padd-left {
      padding-left: 10px;
  
      @media #{$mobile} {
        padding-left: 15px;
      }
    }
  
    .modal-content {
      padding: 60px 40px;
      border-radius: 10px;
      border: 0;
  
      @media #{$mobile} {
        padding: 24px 10px;
      }
  
      .creditModal-header {
        position: relative;
  
        h5 {
          font-size: 24px;
          line-height: 24px;
          margin-bottom: 45px;
  
          @media #{$mobile} {
            margin-bottom: 24px;
            text-align: left !important;
            padding-left: 15px;
          }
        }
      }
      .close {
        width: 48px;
        height: 48px;
        border-radius: 50%;
        border: 1px solid #ccc;
        line-height: 12px;
        padding: 0 !important;
        opacity: 1;
        position: absolute;
        top: -10px;
        right: 15px;
        transition: all 0.3s ease-in-out;
  
        svg {
          transition: all 0.35s ease-in-out;
        }
  
        &:hover {
          svg {
            transform: rotate(180deg);
          }
        }
  
        @media #{$mobile} {
          width: 40px;
          height: 40px;
        }
      }
    }
  
    .modal-body {
      @media #{$mobile} {
        padding: 0 14px;
      }
  
      @media #{$tablet} {
        padding: 0;
      }
  
      .form-group {
        border: 1px solid #e0ded5;
        padding: 15px 30px;
        margin-bottom: 20px;
  
        @media #{$mobile} {
          margin-bottom: 8px;
          padding: 7px 16px;
        }
  
        @media #{$tablet} {
          padding: 12px 20px 15px;
        }
  
        label {
          font-size: 12px;
          line-height: 20px;
          font-weight: 500;
          margin-bottom: 0;
  
          @media #{$mobile} {
          }
  
          svg {
            margin-left: 5px;
          }
        }
  
        input {
          border: 0;
          padding: 0;
          font-weight: 500;
  
          @media #{$mobile} {
            line-height: 20px;
            font-size: 14px;
          }
  
          &::placeholder {
            font-weight: normal;
          }
        }
      }
  
      .save {
        padding: 13px 42px;
        float: right;
        margin-top: 20px;
        font-size: 20px;
        color: #fcf9f2;
  
        @media #{$mobile} {
          line-height: 20px;
          font-size: 16px;
          margin-top: 0;
          display: block;
          width: 100%;
          padding: 18px 0;
        }
      }
    }
  }
  .modal-backdrop.show {
    opacity: 0.7;
  }
  
  .modal-backdrop {
    background-color: #5b5863;
  }
}

