/* Start openvpn configuration */
.openvpn-configuration {
  padding-bottom: 40px;
  &-title {
    font-weight: bold;
    font-size: 24px;
    line-height: 44px;
    border-bottom: 1px solid #ebe8df;
    padding: 15px 0;
    @media #{$mobile } {
      font-size: 18px;
      line-height: 18px;
      padding: 18px 0 16px;
    }
    @media (min-width: 768px) and (max-width: 1199px) {
      font-size: 20px;
      padding: 10px 0;
    }
  }
  &-content {
    background: #ffffff;
    border: 1px solid #ebe8df;
    border-radius: 10px;
    .configuration-info {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 35px 40px;
      position: relative;
      flex-wrap: wrap;
      @media #{$mobile } {
        align-items: stretch;
        flex-direction: column;
        padding: 20px 15px;
        min-height: 338px;
        justify-content: flex-start;
      }
      @media #{$tablet} {
        padding: 25px 20px 5px;
      }
      @media #{$betweentabdesk } {
        padding: 20px 32px 32px;
      }

      &-text {
        flex-basis: 100%;
        text-align: center;
        margin-bottom: 16px;
        line-height: 20px;
        font-weight: 500;
        opacity: 0.7;
        @media #{$mobile} {
          font-size: 14px;
          line-height: 16px;
          width: 80%;
          margin: 0 auto;
          margin-bottom: 20px !important;
        }
      }
      .location-select,.socks_locations {
        flex: 1 1 18%;
        margin: 5px;
        @media #{$tablet} {
          flex: 1 1 34%;
        }
        @media #{$betweentabdesk} {
          flex: 1 1 25%;
        }
        @media #{$mobile } {
          flex: 0 1 18%;
        }
      }
      .protocol-select {
        flex: 1 1 14%;
        margin: 5px;
        @media #{$tablet} {
          flex: 1 1 23%;
        }
        @media #{$mobile } {
          flex: 0 1 14%;
        }
      }
      .download-config {
        flex: 1 1 17%;
        color: #fff;
        font-size: 16px;
        text-align: center;
        padding: 21px 0;
        cursor: pointer;
        margin: 5px;
        cursor: pointer;
        &.button-disabled {
          pointer-events: none;
          opacity: 0.5;
        }
        @media #{$betweentabdesk} {
          flex: 1 1 22%;
        }
        @media #{$tablet} {
          flex: 1 1 26%;
        }
        @media #{$mobile } {
          flex: 0 1 17%;
          min-height: 50px;
          line-height: 50px;
        }

        &:hover {
          color: #fff;
        }
        svg {
          position: relative;
          top: -3px;
          left: -2px;
        }
      }
      .version-select {
        flex: 1 1 10%;
        display: none;
        margin: 5px;
        @media #{$tablet} {
          flex: 0 1 23%;
        }
        @media #{$mobile } {
          flex: 0 1 10%;
        }
      }
      .port-select {
        flex: 1 1 7%;
        display: none;
        margin: 5px;
        @media #{$tablet} {
          flex: 0 1 15%;
        }
        @media #{$mobile } {
          flex: 0 1 7%;
        }
      }
      .show-credentials {
        flex: 1 1 15%;
        background: #ffecf1;
        border-radius: 5px;
        font-weight: 500;
        border: 0;
        color: #fd3e72;
        padding: 21px 0;
        transition: 0.3s;
        margin: 5px;
        &.button-disabled {
          pointer-events: none;
          opacity: 0.6;
        }
        @media #{$betweentabdesk} {
          margin-top: 10px;
        }
        @media #{$tablet} {
          flex: 1 1 26%;
        }
        @media #{$mobile } {
          flex: 0 1 15%;
          padding: 13px 0;
          font-size: 16px;
        }
        &:hover {
          color: #fff;
          background-color: $primaryColor;
        }
      }

      .show-profile,.add-device {
        flex: 1 1 39%;
        padding: 18px 0;
        display: none;
        margin: 5px;
        @media #{$tablet} {
          margin-bottom: 20px;
          flex: 1 1 32%;
          font-size: 16px;
        }
        @media #{$mobile} {
          flex: 0 1 39%;
          min-height: 56px;
          line-height: 56px;
        }
      }
      .item {
        @media #{$tablet} {
          margin-bottom: 10px;
        }
        @media #{$mobile} {
          text-align: center;
        }
      }

      .wireguard_devices {
        margin-top: 40px;
        thead th {
          border-bottom: 1px solid #dee2e6;
          border-top: 0;
        }
        .btns {
          button {
            background: #fd3e72;
            border-radius: 4px;
            width: 30px;
            height: 30px;
            line-height: 0;
            text-align: center;
            margin: 0 5px;
            padding: 0;
            border: 0;
          }
        }
      }
      .add-device,.socks_locations,.wireguard_devices {
        display: none;
      }
      .light-box {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: rgba(91, 88, 99, 0.85);
        backdrop-filter: blur(3px);
        border-radius: 0px 0px 10px 10px;
        display: flex;
        padding: 25px 175px;
        justify-content: space-between;
        align-items: center;
        opacity: 0;
        visibility: hidden;
        transition: all 0.5s ease-in-out;
        transform: translateY(10px);
        &.two {
          padding: 25px;
          @media #{$tablet} {
            transform: translateY(0);
            flex-wrap: wrap;
            padding-top: 10px;
            align-items: flex-start;
            padding-bottom: 0;
          }

          @media #{$mobile} {
            padding: 4px;
          }
          .box {
            flex-basis: 22.5%;
            @media #{$tablet} {
              flex-basis: 49.5%;
            }
          }
          .close {
            position: relative;
            top: 0;
            right: 0;
            @media #{$tablet} {
              position: static;
              width: 100%;
              transform: translateX(0);
            }
          }
        }
        @media #{$betweentabdesk} {
          padding: 25px 124px;
        }
        @media #{$tablet} {
          align-items: flex-start;
          padding: 30px;
          flex-wrap: wrap;
        }
        @media #{$mobile} {
          flex-direction: column;
          padding: 4px;

          align-items: stretch;
          justify-content: flex-start;
        }
        &.is-visible {
          opacity: 1;
          visibility: visible;
          transform: translateY(0);
        }
        .box {
          flex-basis: 49%;
          display: flex;
          align-items: flex-end;
          background-color: #fff;
          padding: 8px 24px;
          justify-content: space-between;
          border-radius: 5px;
          position: relative;
          @media #{$mobile} {
            flex-basis: auto;
            padding: 9px 16px;
            &:not(:last-child) {
              margin-bottom: 4px;
            }
          }
          .field {
            flex: 1;
            label {
              display: block;
              margin-bottom: 0;
              @media #{$mobile} {
                font-size: 14px;
              }
            }
            input {
              border: 0;
              font-weight: 500;
              color: $primaryColor !important;
              width: 100%;
              @media #{$mobile} {
                font-size: 14px;
              }
            }
          }
          .my-tooltip {
            position: relative;
            width: 130px;
            text-align: right;
            position: absolute;
            right: 20px;
            background: linear-gradient(270deg, #ffffff 31.78%, rgba(255, 255, 255, 0) 66.31%);
            button {
              border: 0;
              background: none;
              position: relative;
              padding: 0 5px;
              svg {
                path {
                  transition: all 0.3s ease-in-out;
                }
              }
            }
            &:hover {
              .tooltiptext {
                visibility: visible;
                opacity: 1;
              }
              svg {
                path {
                  fill: #908e91;
                }
              }
            }
            .tooltiptext {
              visibility: hidden;
              background-color: #4f4f4f;
              font-size: 12px;
              color: #fff;
              text-align: center;
              border-radius: 5px;
              padding: 3px 11px;
              position: absolute;
              z-index: 1;
              bottom: 123%;
              left: 50%;
              transform: translatex(-50%);
              opacity: 0;
              transition: opacity 0.3s;
              font-weight: 500;
              text-transform: none;
              @media #{$mobile} {
                font-size: 12px;
                padding: 4px 8px;
              }

              &.changed {
                background-color: #08ae7e;
                &:after {
                  border-color: #08ae7e transparent transparent transparent;
                }
              }
              &:after {
                content: "";
                position: absolute;
                top: 100%;
                left: 50%;
                margin-left: -5px;
                border-width: 5px;
                border-style: solid;
                border-color: #4f4f4f transparent transparent transparent;
              }
            }
          }
        }
        .close {
          position: absolute;
          width: 70px;
          height: 64px;
          line-height: 56px;
          border-radius: 5px;
          border: 1px solid rgba(#fff, 0.3);
          top: 56px;
          right: 40px;
          opacity: 1;
          cursor: pointer;
          text-align: center;
          @media #{$maxTab} {
            position: static;
            display: block;
            width: 100%;
            height: 48px;
            line-height: 44px;
          }
          @media #{$betweentabdesk} {
            top: 46px;
          }
        }
      }
    }
  }
}
