      margin-bottom: 100px;
      background-color: #fff;
      border-radius: 10px;

      @media #{$tablet} {
        margin-top: 50px;
        margin-bottom: 100px;
      }

      th {
        border: 0;
      }

      td {
        border: 0;
        font-size: 14px;
        color: #5b5863;
        height: 64px;
