              border-radius: 10px 0 0 0;
            }
            &:last-child {
              border-radius: 0 10px 0 0;
            }
          }
        }
      }

      tbody {
        tr:not(:last-child) {
          border-bottom: 1px solid #e0ded5;
        }
        th[scope="row"] {
          font-size: 14px;
          color: #8d8b92;
          padding: 0 30px 0 32px;
          font-weight: normal;
          vertical-align: middle;
        }

        td:nth-child(2) {
          a {
            color: $primaryColor;
            font-weight: 500;
            text-decoration: underline !important;
          }
        }

        td:nth-child(4),
        td:nth-child(5) {
          font-weight: 500;
        }
      }
    }

    .invoices {
      > .cell {
        background-color: #fff;
        position: relative;
        padding: 0 28px;
        border-bottom: 1px solid #e0ded5;
        height: 64px;
        line-height: 64px;

        > span {
          font-size: 16px;
          line-height: 20px;
          font-weight: 500;
          color: $primaryColor;
        }

        .arrow {
          top: 22px;
          right: 28px;
        }

        &.active {
          .arrow {
            span {
              background-color: #5b5863;

              &:first-of-type {
                transform: rotate(-45deg);
              }

              &:last-of-type {
                transform: rotate(45deg);
              }
            }
          }
        }
      }

      .toggle {
        display: none;

        .cell {
          padding: 0 28px;
          height: 54px;
          line-height: 54px;
          border-bottom: 1px solid #ebeae5;
          background-color: #fbfaf9;

          &:last-child {
            border-bottom: 1px solid #e0ded5;
          }

          p {
            margin-bottom: 0;
            font-size: 14px;
            color: #8d8b92;

            span {
              float: right;

              &.payment {
                color: #5a5761;
              }

              &.amount,
              &.subscription {
                font-size: 14px;
                color: #5b5863;
              }
            }
          }
        }
      }
    }
  }

  .content-three {
    @media #{$mobile} {
      background: rgba(#f1f1ed, 0.3);
    }

    .subscription-settings {
      margin-top: 80px;
      margin-bottom: 200px;
      box-shadow: 0px 20px 29px rgba(240, 238, 233, 0.8), 0px 6px 9px #e6e4df;
      border-radius: 10px;

      @media #{$mobile} {
        margin-top: 20px;
        margin-bottom: 40px;
      }

      @media #{$tablet} {
        margin-top: 50px;
        margin-bottom: 100px;
      }
    }

    .back-to-dashboard {
      padding: 32px 60px;
      background-color: #f5f4f0;
      text-align: right;
      border-radius: 0 0 10px 10px;

      @media #{$mobile} {
        padding: 10px;
      }

      @media #{$tablet} {
        padding: 30px 40px;
      }

      a {
        padding: 13px 41px;
        display: inline-block;

        @media #{$mobile} {
          width: 100%;
          display: block;
          padding: 16px 0;
          font-size: 16px;
          text-align: center;
        }

        &:hover {
          color: #fff;

          i {
            left: -15px;
          }
        }

        i {
          position: relative;
          transition: all 0.3s ease-in-out;
          left: -7px;
          top: 0;
        }
      }
    }
  }

  .content-three {
    .change-password {
      padding-top: 80px;
      padding-bottom: 100px;

      @media #{$mobile} {
        padding-top: 20px;
        padding-bottom: 40px;
      }

      @media #{$tablet} {
        padding-top: 50px;
        padding-bottom: 80px;
      }
      .password-change-success {
        border-radius: 10px;
        background: #d7f8ee;
        border: 1px solid #95dec9;
        padding: 44px 60px;
        font-weight: bold;
        font-size: 18px;
        line-height: 24px;
        color: #08ae7e;
        margin-bottom: 20px;
        display: none;
        @media #{$mobile} {
          font-size: 14px;
          padding: 24px 14px;
          font-weight: 500;
        }
        @media #{$tablet} {
          padding: 30px;
        }
        svg {
          margin-right: 10px;
          position: relative;
          top: -3px;
          @media #{$mobile} {
            margin-right: 2px;
          }
        }
      }

      .password-2fa {
        background: #ffffff;
        border: 1px solid #ebe8df;
        padding: 30px 60px;
        border-radius: 10px;
        margin-bottom: 20px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        @media #{$mobile} {
          padding: 16px;
        }
        .content {
          display: flex;
          align-items: center;
          .icon {
            margin-right: 20px;
            @media #{$mobile} {
              margin-right: 10px;
              svg {
                width: 32px;
                height: auto;
              }
            }
          }
          .text {
            p {
              font-size: 16px;
              line-height: 20px;
              color: #8d8b92;
              margin-bottom: 10px;
              @media #{$mobile} {
                font-size: 12px;
                line-height: 20px;
                margin-bottom: 0;
              }
            }
            h3 {
              font-size: 20px;
              line-height: 20px;
              font-weight: 500;
              @media #{$mobile} {
                font-size: 13px;
                line-height: 20px;
              }
            }
          }
        }
        .enable-2fa {
          a {
            font-size: 16px;
            padding: 16px 50px;
            @media #{$mobile} {
              padding: 17px;
              min-height: 32px;
              line-height: 32px;
              font-size: 14px;
            }
            &.disable {
              background-color: #e0dfd9;
              color: #5b5863;
            }
          }
        }
        #fa-enable {
          .modal-content {
            text-align: center;
            padding: 60px 135px;
            @media #{$mobile} {
              padding: 24px;
            }
            @media #{$tablet} {
              padding: 60px 70px;
            }
            .modal-title {
              position: relative;
              font-size: 24px;
              @media #{$mobile} {
                font-size: 18px;
                line-height: 18px;
                text-align: left;
              }
              .close {
                right: -75px;
                @media #{$maxTab} {
                  right: 0;
                }
              }
            }
            .fa-enable-content {
              > p {
                width: 73%;
                margin: 10px auto 40px;
                line-height: 28px;
                @media #{$mobile} {
                  font-size: 14px;
                  line-height: 24px;
                  width: 90%;
                  margin: 30px auto 30px;
                }
                a {
                  color: $primaryColor;
                  text-decoration: underline !important;
                }
              }
              img {
                margin: auto;
              }
              > a {
                font-size: 13px;
                line-height: 20px;
                text-decoration-line: underline !important;
                color: #8d8b92;
                margin: 20px 0 40px;
                @media #{$mobile} {
                  margin: 20px 0 30px;
                }
              }
              .secure-key {
                margin: 20px 0 30px;
                p {
                  &:first-child {
                    font-size: 13px;
                    line-height: 20px;
                    color: #8d8b92;
                  }
                  &.key {
                    font-size: 16px;
                    line-height: 20px;
                    color: #5b5863;
                    font-weight: 500;
                    margin-top: 5px;
                  }
                }
              }
              .validate-fa {
                display: flex;
                justify-content: space-between;
                align-items: center;
                @media #{$mobile} {
                  flex-wrap: wrap;
                }
                input {
                  background: #ffffff;
                  border: 1px solid #e0ded5;
                  width: 68%;
                  height: 56px;
                  line-height: 56px;
                  padding-left: 20px;
                  border-radius: 5px;
                  @media #{$mobile} {
                    width: 100%;
                    margin-bottom: 10px;
                    font-size: 12px;
                    line-height: 44px;
                    height: 44px;
                  }
                  font-weight &::placeholder {
                    font-size: 16px;
                    line-height: 20px;
                    color: #8d8b92;
                    font-weight: normal;
                    @media #{$mobile} {
                      font-size: 12px;
                    }
                  }
                }
                button {
                  width: 30%;
                  height: 56px;
                  line-height: 56px;
                  @media #{$mobile} {
                    width: 100%;
                    min-height: 44px;
                    line-height: 44px;
                    height: 44px;
                  }
                }
              }
            }
            .modal-success-2fa {
              text-align: center;
              width: 85%;
              margin: auto;
              @media #{$mobile} {
                width: 100%;
              }

              .success-msg {
                background: #d7f8ee;
                border: 1px solid #95dec9;
                box-sizing: border-box;
                border-radius: 5px;
                display: flex;
                align-items: center;
                margin: 60px 0 50px;
                padding: 26px 40px;
                @media #{$mobile} {
                  padding: 16px 30px 16px 16px;
                  text-align: left;
                  align-items: flex-start;
                  margin: 50px 0 30px;
                }
                span {
                  margin-right: 14px;
                  @media #{$mobile} {
                    margin-right: 10px;
                  }
                }
                p {
                  color: #08ae7e;
                  @media #{$mobile} {
                    font-size: 14px;
                    line-height: 16px;
                  }
                }
              }
              .backup-code {
                h3 {
                  font-size: 18px;
                  line-height: 24px;
                  font-weight: 500;
                  @media #{$mobile} {
                    font-size: 16px;
                    line-height: 20px;
                  }
                }
                .store-code {
                  margin: 15px 0 20px;
                  font-size: 14px;
                  line-height: 14px;
                }
                .code {
                  background: #f9f8f4;
                  border: 1px solid #e3e0d3;
                  border-radius: 2px;
                  height: 48px;
                  line-height: 48px;
                  font-weight: bold;
                  @media #{$mobile} {
                    font-size: 14px;
                  }
                }
                .msg {
                  font-size: 14px;
                  line-height: 20px;
                  margin-top: 20px;
                  @media #{$mobile} {
                    font-size: 13px;
                  }
                }
              }
            }
          }
        }
      }

      .disabled-2fa-msg {
        background: rgba(239, 66, 115, 0.05);
        border: 1px solid #ef4273;
        border-radius: 5px;
        padding: 45px 60px;
        display: flex;
        align-items: center;
        margin-bottom: 20px;
        @media #{$tablet} {
          padding: 30px 40px;
        }
        @media #{$mobile} {
          padding: 16px;
        }
        span {
          margin-right: 15px;
          @media #{$mobile} {
            margin-right: 8px;
            svg {
              width: 24px;
              height: auto;
            }
          }
        }
        p {
          font-weight: bold;
          font-size: 18px;
          line-height: 24px;
          color: #ef4273;
          @media #{$mobile} {
            font-size: 13px;
            line-height: 20px;
          }
        }
      }

      .change-password-info {
        border-radius: 10px;
        background-color: #fff;
        box-shadow: 0px 20px 29px rgba(240, 238, 233, 0.8), 0px 6px 9px #e6e4df;
      }

      &-content {
        padding: 60px;
        @media (min-width: 992px) {
          border-right: 1px solid #e3e0d3;
        }

        @media #{$mobile} {
          padding: 32px 24px;
        }

        @media #{$tablet} {
          padding: 30px;
        }

        form {
          .form-group {
            border: 1px solid #e0ded5;
            padding: 15px 30px;
            margin-bottom: 20px;
            border-radius: 5px;

            &:focus-within {
              label {
                color: #8d8b92;
              }
            }

            @media #{$maxTab} {
              padding: 5px 16px;
              height: 60px;
              margin-bottom: 8px;
            }

            label {
              font-size: 12px;
              line-height: 20px;
              font-weight: 500;
              margin-bottom: 0;
            }

            input {
              border: 0;
              padding-left: 0;
              padding: 0;

              @media #{$maxTab} {
                font-size: 14px;
              }

              &::placeholder {
                font-size: 16px;
                line-height: 20px;
                color: #8d8b92;
                font-weight: normal;

                @media #{$maxTab} {
                  font-size: 14px;
                }
              }
            }
          }

          button {
            width: 100%;
            display: block;
            padding: 13px 0;

            @media #{$maxTab} {
              font-size: 16px;
            }
          }
        }
      }
    }

    .check-password {
      padding: 60px 60px 60px 30px;

      @media #{$mobile} {
        padding: 25px 24px 35px;
        border-bottom: 1px solid rgba(#e3e0d3, 0.7);
      }

      @media #{$tablet} {
        padding: 30px;
        border-bottom: 1px solid rgba(#e3e0d3, 0.7);
      }
      @media #{$betweentabdesk} {
        padding: 60px 30px 0 0px;
      }

      h4 {
        font-size: 16px;
        line-height: 20px;
        color: #5b5863;
        font-weight: 500;
        margin-bottom: 11px;
        width: 75%;

        @media #{$betweentabdesk} {
          width: 100%;
        }
      }

      ul {
        li {
          font-size: 13px;
          line-height: 32px;
          color: #8d8b92;

          svg {
            margin-right: 8px;
            font-size: 16px;
            position: relative;
            top: -1px;
          }

          &.invalid {
            .false {
              display: inline-block;
            }
            .true {
              display: none;
            }
          }

          &.valid {
            .false {
              display: none;
            }
            .true {
              display: inline-block;
            }
          }
        }
      }
      .line-break {
        opacity: 0.7;
        margin: 32px 0;
        @media #{$maxTab} {
          margin: 16px 0;
        }
      }

      .tip {
        strong {
          color: #5b5863;
        }
        font-size: 13px;
        line-height: 20px;
        color: #8d8b92;
        width: 98%;
        @media #{$mobile} {
          width: 100%;
        }
      }
    }
  }

  .content-four {
    .whishlist {
      background: #ffffff;
      border: 1px solid #ebe8df;
      border-radius: 10px;
      margin: 60px 0 30px;
      @media #{$mobile} {
        margin: 20px 0;
      }
      &__content {
        &__title {
          font-size: 24px;
          line-height: 44px;
          margin: 15px 0;
          text-align: center;

          @media #{$betweentabdesk} {
            font-size: 20px;
          }
          @media #{$maxTab} {
            font-size: 18px;
            line-height: 24px;
            padding: 0 40px;
            margin: 20px 0;
            text-align: center;
          }
        }
        &__info {
          border-top: 1px solid #ebe8df;
          border-radius: 0px 0px 10px 10px;
          padding: 28px 40px 90px 40px;
          display: flex;
          justify-content: space-between;
          align-items: center;
          @media #{$tablet} {
            flex-wrap: wrap;
          }
          @media #{$mobile} {
            flex-direction: column;
            align-items: stretch;
            padding: 20px 26px 26px;
          }
          &__text {
            width: 17%;
            font-size: 20px;
            line-height: 20px;
            font-weight: 500;
            text-align: center;
            @media #{$mobile} {
              width: 100%;
              margin-bottom: 14px;
              text-align: center;
            }
          }
          &__ip {
            width: 39%;
            position: relative;
            @media #{$tablet} {
              width: 48%;
            }
            @media #{$mobile} {
              width: 100%;
              margin-bottom: 20px;
            }
            input {
              border: 1px solid #e0ded5;
              border-radius: 5px;
              padding: 21px 30px;
              font-size: 18px;
              line-height: 20px;
              color: #5a5761 !important;
              font-weight: 500;
              width: 100%;
              @media #{$mobile} {
                padding: 17px 24px;
              }
              &::placeholder {
                opacity: 0.3;
              }
              &.is-invalid {
                border: 1px solid #f04545;
              }
            }
            span.ip {
              text-align: center;
              font-size: 22px;
              line-height: 20px;
              color: #5a5761;
              font-weight: 500;
            }
            span.active {
              background: #e5f7ee;
              color: #5fcd93;
              font-weight: bold;
              font-size: 16px;
              padding: 10px 27px;
              margin-left: 10px;
              border-radius: 6px;
              display: inline-block;
            }
            button {
              position: absolute;
              right: 10px;
              top: 10px;
              border: 0;
              padding: 0 20px;
              display: none;
              background: #ffecf1;
              border-radius: 5px;
              @media #{$mobile} {
                top: 7px;
                right: 7px;
                padding: 0 14px;
              }
              span {
                font-size: 14px;
                line-height: 46px;
                font-weight: 500;
                color: $primaryColor;
                @media #{$mobile} {
                  line-height: 42px;
                }
              }
              &:disabled {
                opacity: 0.5;
                cursor: not-allowed;
              }
              &.success {
                display: inline-block;
                background: none;
                cursor: text;
                svg {
                  path {
                    fill: #08ae7e;
                  }
                }
                span {
                  color: #08ae7e;
                }
              }
            }

            .error {
              position: absolute;
              bottom: -23px;
              left: 30px;
              font-size: 12px;
              line-height: 20px;
              color: #f04545;
              display: none;
              @media #{$mobile} {
                position: relative;
                bottom: -2px;
                left: 0;
              }

              svg {
                margin-right: 5px;
                position: relative;
                top: -1px;
              }
            }
            .auto-detect-ip {
              text-align: center;
              font-size: 16px;
              line-height: 20px;
              font-weight: 500;
              text-decoration-line: underline !important;
              margin-top: 40px;
              color: #fd3e72;
              position: absolute;
              left: 50%;
              transform: translateX(-50%);
              width: 100%;
              @media #{$mobile} {
                position: relative;
                margin-top: 30px;
              }
            }
          }
          &__ip-controls {
            width: 41%;
            display: flex;
            justify-content: space-between;
            align-items: center;
            @media #{$betweentabdesk} {
              width: 41%;
            }
            @media #{$tablet} {
              width: 30%;
            }
            @media #{$mobile} {
              width: 100%;
              flex-direction: column;
            }
            .save-ip,
            .change-ip {
              width: 48%;
              padding: 20px 0;
              font-size: 16px;
              display: flex;
              align-items: center;
              justify-content: center;
              @media #{$mobile} {
                width: 100%;
                margin-bottom: 8px;
              }
              @media #{$tablet} {
                width: 100%;
              }
              svg {
                margin-right: 12px;
                path {
                  transition: all 0.3s ease-in-out;
                }
              }
            }

            .remove-ip {
              &:disabled {
                opacity: 0.5;
                cursor: not-allowed;
                &:hover {
                  background-color: #ffecf1;
                  color: #fd3e72;
                  svg {
                    path {
                      fill: $primaryColor;
                    }
                  }
                }
              }
              @media #{$mobile} {
                margin-bottom: 0;
              }
            }
          }
        }
      }
      &.activated-ip {
        .whishlist__content__info {
          padding: 28px 40px;
        }
        .whishlist__content__info__ip-controls {
          width: 21%;
          @media #{$mobile} {
            width: 100%;
          }
          .change-ip {
            width: 100%;
          }
        }
        .whishlist__content__info__ip {
          width: 49%;
          text-align: center;
          @media #{$mobile} {
            width: 100%;
          }
        }
        .whishlist__content__info__text {
          width: 35%;
          font-size: 18px;
          @media #{$mobile} {
            width: 100%;
            margin-bottom: 10px;
            text-align: center;
          }
        }
        @media #{$betweentabdesk} {
          .whishlist__content__info__text {
            width: 23%;
          }
          .whishlist__content__info__ip {
            width: 35%;
          }
        }
        @media #{$tablet} {
          .whishlist__content__info {
            padding: 28px 20px;
          }
          .whishlist__content__info__text {
            width: 30%;
          }
          .whishlist__content__info__ip {
            width: 46%;
            .auto-detect-ip {
              margin-top: 20px;
            }
          }
          .whishlist__content__info__ip-controls {
            width: 22%;
            a,
            button {
              width: 100%;
              padding: 15px 0;
              font-size: 14px;
            }
          }
        }
      }
    }
    .alert-warning {
      padding: 25px 42px 34px 42px;
      font-size: 24px;
      line-height: 42px;
      color: #e87b2c;
      background: #ffebcd;
      border-radius: 8px;
      margin: 50px 0;
      display: flex;
      align-items: center;
      @media #{$betweentabdesk} {
        margin: 32px 0 150px;
        font-size: 20px;
      }
      @media #{$tablet} {
        padding: 25px 100px 34px 42px;
        margin: 32px 0 100px;
        font-size: 20px;
      }
      @media #{$mobile} {
        padding: 25px 42px 34px 42px;
        margin: 32px 0;
        font-size: 16px;
        line-height: 28px;
        align-items: flex-start;
        p {
          flex: 1;
        }
      }
      svg {
        margin-right: 10px;
      }
    }
    .smartdns-pending {
      padding: 100px 0;
      background: #ffffff;
      border: 1px solid #ebe8df;
      border-radius: 10px;
      margin: 60px 0;
      display: none;
      @media #{$tablet} {
        padding: 100px 130px;
      }
      @media #{$mobile} {
        padding: 60px 20px;
      }
      h2 {
        display: flex;
        justify-content: space-between;
        align-items: center;
        @media #{$mobile} {
          flex-direction: column;
          .pending {
            margin-top: 20px;
          }
        }
        > span {
          font-size: 50px;
          line-height: 44px;
          text-align: center;
          color: #5b5863;
        }
        .pending {
          font-size: 16px;
          line-height: 20px;
          font-weight: 500;
          color: $primaryColor;
          background: #ffecf1;
          border-radius: 4px;
          width: 140px;
          height: 40px;
          line-height: 40px;
          text-align: center;

          .spinner-border {
            border-width: 2px;
            color: $primaryColor;
            width: 20px;
            height: 20px;
            margin-left: 5px;
          }
        }
      }
      h4 {
        font-size: 24px;
        line-height: 44px;
        margin: 20px 0 25px;
        @media #{$mobile} {
          font-size: 20px;
        }
      }
      p {
        font-size: 18px;
        line-height: 30px;
        color: #959399;
      }
    }
    .smartdns-active-ip {
      display: none;
      .whishlist__content__info__text {
        text-align: left;
        line-height: 26px;
        width: 20%;
      }
    }
    .modal-content {
      padding: 60px;
      @media #{$mobile} {
        padding: 24px;
      }
      .change-ip-content {
        p {
          font-size: 16px;
          line-height: 24px;
          color: #8d8b92;
          margin: 30px 0 15px;
        }
        .old-ip,
        .new-ip {
          position: relative;
          .ip {
            font-weight: bold;
            font-size: 24px;
            line-height: 24px;
          }
          input.ip {
            border: 1px solid #e0ded5;
            border-radius: 5px;
            padding: 21px 30px;
            line-height: 20px;
            color: #5a5761 !important;
            font-weight: bold;
            font-size: 24px;
            &::placeholder {
              color: #999;
              font-weight: normal;
            }
            @media #{$mobile} {
              padding: 15px;
              width: 100%;
            }
          }
          .auto-detect-ip {
            text-align: center;
            font-size: 18px;
            line-height: 20px;
            font-weight: 500;
            text-decoration-line: underline !important;
            color: #fd3e72;
            margin-left: 20px;
            @media #{$mobile} {
              margin-left: 0;
              text-align: center;
              display: block;
              margin-top: 50px;
            }
          }
          .error {
            position: absolute;
            bottom: -23px;
            left: 30px;
            font-size: 12px;
            line-height: 20px;
            color: #f04545;
            display: none;
            margin: 0;
            @media #{$mobile} {
              bottom: 38px;
              left: 30px;
              position: absolute;
            }

            svg {
              margin-right: 5px;
              position: relative;
              top: -1px;
            }
          }
        }
        a.change-ip {
          width: 217px;
          height: 64px;
          line-height: 64px;
          display: block;
          font-size: 16px;
          margin-top: 40px;
          @media #{$mobile} {
            width: 100%;
          }
        }
      }
    }

    .modal-title {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding-bottom: 34px;
      border-bottom: 1px solid rgba(#e3e0d3, 0.7);
      @media #{$mobile} {
        padding-bottom: 15px;
      }
      span {
        font-size: 24px;
        line-height: 24px;
        @media #{$mobile} {
          font-size: 18px;
        }
      }
      .close {
        position: static;
      }
    }
    .smartdns-servers {
      background: #ffffff;
      border-radius: 10px;
      margin-bottom: 60px;
      @media #{$mobile} {
        margin-bottom: 20px;
      }

      &__title {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 18px 40px;
        border-bottom: 1px solid rgba(#e3e0d3, 0.4);
        @media #{$tablet} {
          padding: 22px 30px;
        }
        @media #{$mobile} {
          padding: 22px 24px;
        }
        span {
          font-size: 24px;
          line-height: 44px;
          @media #{$mobile} {
