.content-five .form-group {
  position: relative;
  min-height: 80px;

  @media #{$mobile} {
    min-height: 60px;
  }
}

.list-content .select2-container {
  width: 100% !important;
  outline: none;
  &:focus {
    outline: none;
  }
}

.body-dashboard {
  .select2-container--default .select2-selection--multiple {
    border: 0;
    position: relative;
    z-index: 1;
    background: none;
    &:after {
      content: "";
      background-image: url(../imgs/arrow-down.png);
      display: block;
      position: absolute;
      background-position: center;
      background-repeat: no-repeat;
      z-index: -1;
      width: 20px;
      height: 20px;
      top: 50%;
      right: 24px;
      transform: translateY(-50%);
      cursor: pointer;
      transition: all 0.3s ease-in-out;
      @media #{$mobile} {
        transform: translateY(-60%);
      }
    }
  }

  .select2-container--open {
    .select2-selection--multiple {
      &:after {
        transform: rotate(180deg) translateY(14px);
      }
    }
  }

  .select2-container--default.select2-container--focus .select2-selection--multiple {
    border: 0;
    background: none;
    @media #{$mobile} {
      padding-left: 13px;
    }
  }

  .select2-container--default .select2-results__option[aria-selected="true"] {
    background: none;

    &:before {
      display: block;
    }
  }

  .select2-container--default .select2-results__option--highlighted[aria-selected] {
    background: none;
    color: inherit;
  }

  .select2-container--default .select2-selection--multiple .select2-selection__choice {
    background-color: #fff;
    border: 1px solid #c4c3bb;
    border-radius: 100px;
    padding: 2px 5px;
    font-size: 12px;
  }

  .select2-container--default .select2-selection--multiple .select2-selection__rendered {
    width: 90%;
    padding-left: 30px;
  }

  .select2-container--default .select2-selection--multiple .select2-selection__choice__remove {
    float: right;
    margin-left: 4px;
  }

  .select2-container--open .select2-dropdown--below,
  .select2-container--open .select2-dropdown--above {
    padding-top: 5px;
    // box-sizing: content-box;
  }

  .select2-results__option {
    position: relative;
    border-bottom: 1px solid #e0ded5;
    height: 48px;
    line-height: 48px;
    padding: 0;
    padding-left: 30px;
    font-size: 14px;
    font-weight: 500;
    span {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      display: flex;
      align-items: center;
      img {
        margin-right: 10px;
      }
    }

    @media #{$mobile} {
      padding-left: 20px;
    }

    img {
      margin-right: 4px;
    }
  }

  .select2-dropdown {
    border-color: #e0ded5;
    border-radius: 0;
    border-top: 0;
  }

  .select2-container--default .select2-results > .select2-results__options {
    max-height: 288px;
  }

  .select2-search__field {
    padding-left: 0 !important;
  }
}

.weplay-parent {
  .select2-container--open .select2-dropdown--below,
  .select2-container--open .select2-dropdown--above {
    box-sizing: content-box;
    margin-left: -1px;
  }
}

.vpn-configuration .select2-container--open .select2-dropdown--below,
.vpn-configuration .select2-container--open .select2-dropdown--above,
.vpn-configuration .select2-search--dropdown {
  padding: 0;
  // margin-left: -1px;
}

.vpn-configuration .select2-search__field,
.vpn-configuration .select2-results__option:after,
.vpn-configuration .select2-results__option:after,
.vpn-configuration .select2-container--default .select2-selection--single .select2-selection__arrow b,
.vpn-configuration .select2-container--default .select2-results__option[aria-selected="true"]:before {
  display: none;
}

.select2-container--default.select2-container--open .select2-selection--single .select2-selection__arrow {
  transform: rotate(180deg) translateY(14px);
}

.select2-container--default .select2-selection--single .select2-selection__arrow {
  top: 55%;
  transform: translateY(-50%);
  right: 11px;
  background-image: url(../imgs/arrow-down.png);
  background-repeat: no-repeat;
  background-position: center;
  transform-origin: center;
  transition: all 0.3s ease-in-out;
}

.vpn-configuration .select2-container .select2-selection--single .select2-selection__rendered {
  padding-left: 15px;
  padding-right: 32px;
  white-space: nowrap;
  line-height: 64px;
  color: #5a5761;
  font-weight: 500;

  @media #{$mobile} {
    text-align: left;
    line-height: 54px;
    font-size: 14px;
  }
}

.vpn-configuration .select2-container--default .select2-selection--single {
  border: 1px solid #e0ded5;
  border-radius: 5px;
  height: 100%;
  outline: none;
  box-shadow: none;
}

.select2-container--default .select2-selection--single .select2-selection__placeholder {
  font-weight: normal;
  @media #{$mobile} {
    font-size: 14px;
  }
}

.select2-dropdown {
  position: static;
}
.select2-dropdown.select-dropdown--above {
  margin-top: 336px;
}
