.cont-one {
  .recommended-apps {
    margin: 30px 0;
    @media #{$maxTab} {
      margin: 0 0 30px;
    }
    .content {
      background: linear-gradient(115deg, #fe6556 -12.43%, #fd3e72 55.94%, #49a8ff 152.76%);
      box-shadow: 0px 6px 18px #e6e6e6;
      border-radius: 10px;
      padding: 0 30px 15px;
      @media #{$betweentabdesk} {
        padding: 0 20px 10px;
      }
      @media #{$tablet} {
        padding: 0 15px 15px;
      }
      @media #{$mobile} {
        background: linear-gradient(137.53deg, #fe7e56 -13.93%, #fd3e72 58.35%, #49a8ff 160.72%);
        padding: 0 12px 12px;
      }
      .row {
        > div {
          display: flex;
          justify-content: center;
          @media #{$betweentabdesk} {
            padding: 0 5px;
          }
        }
      }
    }
    .title {
      font-size: 24px;
      line-height: 44px;
      color: #ffffff;
      padding: 5px 0;
      font-weight: 500;
      @media (max-width: 1199px) {
        font-size: 22px;
      }
    }
    .phone-download-mobile {
      @media (min-width: 992px) {
        width: 100%;
        display: flex;
      }
      @media #{$tablet} {
        width: 100%;
      }
    }
    @media #{$mobile} {
      .phone-download-mobile {
        background: #ffffff;
        box-shadow: 0px 4px 21px #eb3264;
        border-radius: 10px;
        padding: 25px 15px 20px;
        .app-download-btn {
          width: 100%;
          background-color: #111;
          border-radius: 5px;
          text-align: center;
          line-height: 50px;
        }
        .line-dashed {
          width: 80%;
          margin: 20px auto;
          height: 1px;
          border-bottom: 1px dashed #e3e0d3;
        }
        
      }
    }

    .download-item {
      background: #ffffff;
      box-shadow: 0px 4px 21px #eb3264;
      border-radius: 10px;
      padding: 15px 0;
      display: flex;
      align-items: center;
      justify-content: space-between;
      text-align: center;
      width: 100%;
      @media #{$mobile} {
        padding: 0;
        background: none;
        box-shadow: none;
        border-radius: 0;
      }
      
      .wevpn-link {
        font-size: 14px;
        margin-top: 10px;
      }
      .image,
      .info {
        flex-basis: 50%;
        padding: 0 15px;
        @media #{$betweentabdesk} {
          padding: 0 12px;
        }
        @media #{$mobile} {
          padding: 0;
        }
        img {
          max-width: 150px;
          margin: auto;
        }
      }
      .image {
        &.windows-download-img {
          img {
            max-width: 210px;
          }
        }
        @media #{$mobile} {
          flex-basis: 61%;
        }
      }
      .info {
        @media #{$mobile} {
          text-align: left;
          margin-left: 10px;
          flex-basis: 35%;
        }
      }
      .info {
        position: relative;
        &::before {
          content: "";
          position: absolute;
          height: 100%;
          border-right: 1px dashed #e3e0d3;
          left: 0;
          top: 0;
          @media #{$mobile} {
            display: none;
          }
        }
        h4 {
          font-weight: 500;
          font-size: 20px;
          line-height: 20px;
        }
        small {
          font-size: 14px;
          line-height: 16px;
          text-align: center;
          color: rgba(91, 88, 99, 0.8);
        }
        p {
          font-size: 14px;
          line-height: 16px;
          margin: 7px 0 11px;
          opacity: 0.7;
          @media (min-width: 1199px) and (max-width: 1399px) {
            font-size: 12px;
          }
          @media #{$mobile} {
            margin: 7px 0 0;
          }
        }
        .download-btn {
          width: 140px;
          height: 40px;
          line-height: 40px;
          font-size: 16px;
          @media #{$betweentabdesk} {
            width: 120px;
            height: 34px;
            line-height: 34px;
            font-size: 14px;
          }
        }
        
        .app-btn {
          transition: all 0.3s ease-in-out;
          @media #{$betweentabdesk} {
            width: 120px;
            img {
              width: 120px;
            }
          }
          &:hover {
            opacity: 0.7;
          }
        }
      }
    }
  }
  .my-devices {
    border-top: 10px solid $primaryColor;
    .warning-msg {
      padding: 15px;
    }
    .warning-content {
      background: #FFECF1;
      border-radius: 5px;
      padding: 10px;
      svg {
        margin-right: 10px;
        min-width: 21px;
      }
      p {
        font-size: 14px;
      }
    }
    .owl-carousel.off {
      display: flex;
    }
    .owl-theme .owl-nav [class*="owl-"]:hover {
      background-color: #fff;
    }
    .owl-theme .owl-dots .owl-dot.active span {
      background-color: $primaryColor;
    }
    .owl-dots {
      margin-top: 15px;
      position: relative;
      bottom: -10px;
      left: 50%;
      transform: translateX(-50%);
      @media #{$tablet} {
        bottom: 10px;
      }
    }
    .owl-nav {
      button.owl-prev,
      button.owl-next {
        position: absolute;
        left: 70px;
        bottom: 10px;
        background-image: url(../imgs/dashboard/owl-prev.svg);
        width: 34px;
        height: 34px;
        background-repeat: no-repeat;
        span {
          display: none;
        }
      }
      button.owl-next {
        left: auto;
        right: 70px;
        background-image: url(../imgs/dashboard/owl-next.svg);
      }
    }
    .box-title {
      display: flex;
      justify-content: space-between;
      align-items: center;
      @media #{$mobile} {
        flex-direction: column;
        border-bottom: 0;
        line-height: 1;
      }
      h2 {
        font-size: 24px;
        line-height: 1;
        margin-bottom: 8px;
        @media #{$mobile} {
          font-size: 22px;
          line-height: 34px;
          margin: 10px 0;
        }
      }
      .text {
        font-size: 14px;
        line-height: 1;
        @media #{$mobile} {
          display: none;
        }
      }
      .devices-install {
        font-weight: 500;
        font-size: 22px;
        @media #{$mobile} {
          font-size: 20px;
        }
        span.devices_num {
          color: $primaryColor;
        }
      }
    }
    .devices {
      display: flex;
      flex-wrap: wrap;
      justify-content: flex-start;
      padding: 15px 15px 5px;
      border-bottom: 1px solid #E3E0D3;
      @media #{$mobile} {
        padding: 25px 0;
      }
      .device {
        margin: 0 6px 18px;
        flex-basis: 15%;
        min-width: 185px;
        @media (min-width: 1600px) {
          margin: 0 9px 18px;
        }
        @media #{$mobile} {
          display: flex;
          flex-direction: column;
          min-width: 105px;
          padding: 12px 6px 6px;
          margin: 0;
        }
        input[type=radio]:checked + label{
          border: 1px solid #f00;
        }
        label {
          padding: 10px;
          border: 1px solid #f4f4f4;
          border-radius: 8px;
          display: block;
          position: relative;
          .new-green-tag {
            position: absolute;
            right: 5px;
            top: 5px;
            font-size: 9px;
            padding: 0 4px;
            line-height: 15px;
          }
        }
        .info {
          display: flex;
          align-items: center;
          @media #{$mobile} {
            flex-direction: column;
          }
          .icon {
            width: 40px;
            text-align: center;
            height: 30px;
            line-height: 30px;
            img {
              height: 30px;
            }
            @media #{$mobile} {
              margin-bottom: 10px;
            }
            @media #{$maxTab} {
              img {
                max-width: 100%;
                width: auto;
                margin: auto;
              }
            }
          }
          .device-text {
            margin-left: 8px;
            @media #{$mobile} {
              margin-left: 0;
              text-align: center;
            }
            h3 {
              font-size: 14px;
              line-height: 20px;
            }
            p {
              font-size: 12px;
              color: rgba(#5b5863, 0.7);
            }
          }
        }

        .sign-out {
          margin-top: 12px;
          a {
            font-size: 14px;
            height: 34px;
            line-height: 34px;
            display: block;
            min-height: auto;
            @media #{$mobile} {
              font-weight: 400;
            }
          }
        }
      }
    }
  }
  .rename-device {
    .rename-device-box {
      position: relative;
      margin: 20px;
    }
    .form-group {
      height: 64px;
      background: #ffffff;
      border: 1px solid #e0ded5;
      border-radius: 5px;
      padding: 4px 18px;
    }
    label {
      font-weight: 500;
      font-size: 12px;
      line-height: 20px;
      margin-bottom: 0;
    }
    input {
      display: block;
      border: 0;
      width: 100%;
      font-weight: 500;
      &::placeholder {
        font-size: 16px;
        line-height: 20px;
        color: #8d8b92;
        font-weight: normal;
      }
    }
    .rename-btn {
      font-size: 14px;
      width: 90px;
      height: 34px;
      line-height: 34px;
      position: absolute;
      right: 15px;
      top: 50%;
      transform: translateY(-50%);
    }
  }
  .dashboard-apps {
    @media #{$mobile} {
      background-color: #fff;
      background: #ffffff;
      box-shadow: 0px 6px 18px #ece9e2;
      border-radius: 10px;
      padding: 25px 15px 15px;
      margin-top: 30px;
    }

    .apps-title {
      font-size: 24px;
      line-height: 44px;
      margin: 30px 0 15px;
      @media #{$mobile} {
        margin: 40px 0 10px;
        &.mrtop-0 {
          margin-top: 0;
        }
      }
    }
    .row {
      > div {
        display: flex;
        @media #{$betweentabdesk} {
          padding: 0 10px;
        }
        @media #{$tablet} {
          padding: 0 7px;
        }
      }
    }
    .app {
      text-align: center;
      width: 100%;
      padding: 30px 0 25px;
      @media #{$mobile} {
        display: flex;
        align-items: center;
        justify-content: space-between;
        background: none;
        box-shadow: none;
        text-align: left;

        border-bottom: 1px solid #f7f7f7;
        padding: 12px 0;
        &.last {
          border-bottom: 0;
        }
      }
      .app-content {
        @media #{$mobile} {
          display: flex;
          align-items: center;
        }
      }
      .icon {
        width: 115px;
        height: 72px;
        line-height: 72px;
        text-align: center;
        margin: auto;
        @media #{$tablet} {
          svg,
          img {
            transform: scale(0.8);
          }
        }
        @media #{$mobile} {
          width: 32px;
          height: 26px;
          margin: 0 10px 0 0;
          line-height: 1;
          svg,
          img {
            width: 100%;
            height: 100%;
          }
        }
      }
      h3 {
        font-weight: 500;
        font-size: 18px;
        line-height: 20px;
        color: #5a5761;
        margin: 25px 0 2px;
        @media #{$mobile} {
          margin: 0 10px 0 0;
          display: inline-block;
        }
      }
      .apk-btn {
        @media #{$mobile} {
          font-size: 14px;
        }
      }
      small {
        font-size: 12px;
        line-height: 16px;
        color: #8d8b92;
      }
      p {
        font-size: 12px;
        line-height: 16px;
        color: #8d8b92;
        margin: 13px 0 20px;
        @media #{$mobile} {
          margin: 0;
        }
      }
      .download-btn {
        width: 173px;
        height: 50px;
        line-height: 50px;
        font-size: 18px;
        display: inline-block;
        transition: all 0.3s ease-in-out;
        @media (max-width: 1199px) {
          font-size: 16px;
        }
        @media #{$mobile} {
          width: 40px;
          height: 40px;
          line-height: 40px;
          min-height: 40px;
          background-color: $primaryColor;
          border-radius: 5px;
          text-align: center;
          display: flex;
          min-width: 40px;
    margin-left: 10px;
        }
        img {
          width: 100%;
          @media #{$mobile} {
            width: auto;
            margin: auto;
          }
        }
        &.app-store-btn:hover {
          opacity: 0.7;
        }
      }
    }
  }
}