.cont-six {
  .account-settings {
    > .row {
      > div {
        display: flex;
      }
    }
    .change-password {
      border-top: 10px solid $primaryColor;
      margin: 40px 0 30px;
      @media #{$maxTab} {
        margin-top: 0;
      }
      .title {
        height: 71px;
        line-height: 71px;
        padding: 0 30px;
        border-bottom: 1px solid rgba(227, 224, 211, 0.4);
        @media #{$mobile} {
          padding: 0 16px;
        }
        h2 {
          font-size: 24px;
          line-height: 1;
          margin-bottom: 8px;
          padding-top: 12px;
          @media #{$mobile} {
            font-size: 22px;
          }
        }
        .last-update {
          font-size: 14px;
          line-height: 1;
          .date {
            color: $primaryColor;
          }
        }
      }
      &-content {
        padding: 16px 30px 20px;
        @media #{$betweentabdesk} {
          padding: 16px 20px 20px;
        }
        @media #{$mobile} {
          padding: 16px 16px 25px;
        }
        > .row {
          > div {
            display: flex;
          }
        }
      }
      .left-col {
        width: 100%;
      }
      .padd-left-0 {
        padding-left: 0;
      }
      .right-col {
        width: 100%;
        background: linear-gradient(147.75deg, #fe6556 -41.87%, #fd3e72 61.46%, #49a8ff 152.18%);
        color: #fff;
        padding: 15px 30px;
        border-radius: 10px;
        position: relative;
        @media #{$betweentabdesk} {
          padding: 15px;
        }
        &::before {
          content: "";
          position: absolute;
          border-width: 22px;
          border-color: transparent #fd4c67 transparent transparent;
          display: block;
          top: 50%;
          left: -42px;
          border-style: solid;
          transform: translateY(-50%);
        }
        @media #{$maxTab} {
          margin-bottom: 20px;
          padding: 15px;
          &:after,
          &:before {
            display: none;
          }
        }
        .check-password {
          h4 {
            font-size: 18px;
            line-height: 25px;
            margin-bottom: 15px;
            @media #{$betweentabdesk} {
              font-size: 16px;
              line-height: 22px;
            }
          }

          ul {
            li {
              font-size: 14px;
              line-height: 20px;
              display: flex;
              &:not(:last-child) {
                margin-bottom: 15px;
                @media #{$betweentabdesk} {
                  margin-bottom: 10px;
                }
              }

              img {
                margin-right: 8px;
                position: relative;
                top: -1px;
              }

              &.invalid {
                .false {
                  display: inline-block;
                }
                .true {
                  display: none;
                }
              }

              &.valid {
                .false {
                  display: none;
                }
                .true {
                  display: inline-block;
                }
              }
            }
          }
          .line-break {
            margin: 15px 0;
            width: 90%;
            opacity: 0.4;
            border-color: #e12155;
          }

          .tip {
            font-size: 14px;
            line-height: 20px;
            max-width: 330px;
            @media #{$tablet} {
              max-width: none;
            }
            strong {
              font-weight: 500;
            }
          }
        }
      }
    }
    form {
      .form-group {
        border: 1px solid #e0ded5;
        padding: 15px 30px;
        margin-bottom: 25px;
        border-radius: 5px;
        height: 80px;

        &:focus-within {
          label {
            color: #8d8b92;
          }
        }
        @media #{$betweentabdesk} {
          padding: 15px;
        }
        @media #{$maxTab} {
          margin-bottom: 15px;
          padding: 15px 20px;
        }

        label {
          font-size: 12px;
          line-height: 20px;
          font-weight: 500;
          margin-bottom: 0;
        }

        input {
          border: 0;
          padding-left: 0;
          padding: 0;
          color: #5b5863;
          font-weight: 500;
          @media #{$maxTab} {
            font-size: 16px;
          }

          &::placeholder {
            color: #8d8b92;
            font-weight: normal;
          }
        }
      }

      button {
        width: 100%;
        display: block;
        height: 50px;
        line-height: 50px;
        font-size: 18px;
        min-height: 50px;
        @media #{$mobile} {
          margin-top: 10px;
        }
      }
    }

    .change-email {
      border-top: 10px solid $primaryColor;
      width: 100%;
      .box-title {
        @media #{$betweentabdesk} {
          padding: 0 20px;
        }
      }
      .content {
        padding: 20px 30px;
        @media #{$betweentabdesk} {
          padding: 20px;
        }
        @media #{$mobile} {
          padding: 20px 16px 25px;
        }
        > p {
          line-height: 26px;
          color: #8d8b92;
          margin-bottom: 6px;
        }
        > h3 {
          font-weight: 500;
          font-size: 18px;
          line-height: 25px;
          color: $primaryColor;
          margin-bottom: 20px;
        }
        form.change-email-form {
          .form-group {
            margin-bottom: 0;
          }
          .save-email {
            .verification-text {
              margin: 20px 0;
              color: #8d8b92;
            }
          }
          .verification {
            .success-msg {
              display: flex;
              align-items: center;
              margin: 10px 0 30px;
              background: rgba(8, 174, 126, 0.1);
              border-radius: 5px;
              padding: 12px 17px;
              span {
                margin-right: 10px;
              }
              p {
                line-height: 20px;
                .new-email {
                  color: $primaryColor;
                }
              }
            }
            a {
              display: block;
              height: 50px;
              line-height: 50px;
              font-size: 18px;
              min-height: 50px;
              @media #{$betweentabdesk} {
                font-size: 16px;
              }
              &.cancel-request {
                text-align: center;
                color: #fd3e72;
                border: 1px solid #fd3e72;
                border-radius: 5px;
                font-weight: 500;
                @media #{$maxTab} {
                  margin-top: 15px;
                }
              }
            }
          }
        }
      }
    }
    .two-factor {
      border-top: 10px solid $primaryColor;
      width: 100%;
      @media #{$maxTab} {
        margin-top: 30px;
      }
      .box-title {
        @media #{$betweentabdesk} {
          padding: 0 20px;
        }
      }
      .content {
        padding: 20px 30px;
        @media #{$betweentabdesk} {
          padding: 20px;
        }
        @media #{$mobile} {
          padding: 20px 16px 30px;
        }
        .text {
          font-size: 16px;
          line-height: 28px;
          color: #8d8b92;
          @media #{$mobile} {
            font-size: 14px;
          }
        }
        .line-break {
          border-bottom: 1px dashed #e3e0d3;
          margin: 30px 0;
        }
        .toggle-2fa {
          display: flex;
          align-items: center;
          justify-content: space-between;
          h3 {
            font-weight: 500;
            font-size: 18px;
            line-height: 25px;
            color: $primaryColor;
          }
        }
      }
    }
  }
  #change-password,
  #change-email {
    .modal-dialog {
      margin: 16px;
      @media (min-width: 576px) {
        max-width: 430px;
        margin: auto;
      }
    }
    .change-password-content,
    .change-email-content {
      padding: 30px;
      @media #{$mobile} {
        padding: 45px 15px 20px;
      }
      img {
        @media #{$mobile} {
          width: 160px;
          margin: auto;
        }
      }
      h3 {
        font-size: 20px;
        line-height: 120%;
        margin: 30px 0 20px;
        @media #{$mobile} {
          margin: 25px 0 15px;
          font-size: 18px;
        }
      }
      p {
        font-size: 14px;
        line-height: 20px;
        text-align: center;
        color: #5a5761;
        opacity: 0.7;
        margin-bottom: 40px;
        @media #{$mobile} {
          margin-bottom: 25px;
        }
      }
      h5 {
        font-size: 14px;
        line-height: 20px;
        color: #5a5761;
        font-weight: 500;
        margin-bottom: 15px;
      }
      button {
        display: block;
        width: 100%;
        height: 56px;
        line-height: 56px;
        padding: 0;
        &.white_btn {
          border: 1px solid $primaryColor;
          margin-bottom: 20px;
        }
        @media #{$mobile} {
          height: 50px;
          line-height: 50px;
          min-height: 50px;
          font-size: 18px;
        }
      }
    }
  }
  #twoFactor {
    .modal-title {
      font-size: 24px;
      line-height: 44px;
      padding: 13px;
      border-bottom: 1px solid rgba(227, 224, 211, 0.4);
      text-align: center;
      @media #{$mobile} {
        font-size: 18px;
        text-align: left;
      }
    }
    .two-factor-content {
      padding: 30px;
      @media #{$mobile} {
        padding: 30px 15px;
      }
      p {
        font-size: 14px;
        line-height: 140%;
        
      }
      hr {
        opacity: 0.4;
        border-top: 1px dashed #D7D7D7;
      }
      > img {
        margin: 10px 0;
      }
      h6 {
        font-weight: bold;
        font-size: 14px;
        line-height: 140%;
        span {
          color: $primaryColor;
        }
      }
      form {
        position: relative;
        margin-top: 20px;
        text-align: left;
        .form-group {
          height: 80px;
          background: #ffffff;
          border: 1px solid #e0ded5;
          border-radius: 5px;
          padding: 16px;
        }
        label {
          font-weight: 500;
          font-size: 12px;
          line-height: 20px;
          margin-bottom: 0;
        }
        input {
          display: block;
          border: 0;
          width: 100%;
        }
        button.submit {
          background: $primaryColor;
          font-size: 16px;
          width: 85px;
          height: 50px;
          line-height: 50px;
          position: absolute;
          right: 15px;
          top: 50%;
          transform: translateY(-50%);
          margin: 0;
        }
      }
      .cancel {
        display: block;
          width: 100%;
          height: 50px;
          line-height: 50px;
          min-height: 50px;
          padding: 0;
          font-size: 16px;
          &.white_btn {
            border: 1px solid $primaryColor;
            margin-top: 20px;
          }
      }
    }
    .modal-two-factor-enabled {
      display: none;
      padding: 30px;
      position: relative;
      @media #{$mobile} {
        padding: 30px 15px;
      }
      .modal-success-2fa {
        text-align: center;
        .success-msg {
          background: #d7f8ee;
          border: 1px solid #95dec9;
          box-sizing: border-box;
          border-radius: 5px;
          display: flex;
          align-items: center;
          margin: 0 0 30px;
          padding: 16px 0px;
          justify-content: center;
          @media #{$mobile} {
            text-align: left;
            align-items: flex-start;
            margin: 0 0 30px;
            padding: 16px;
          }
          span {
            margin-right: 8px;
          }
          p {
            color: #08ae7e;
            @media #{$mobile} {
              font-size: 14px;
              line-height: 16px;
            }
          }
        }
        .backup-code {
          h3 {
            font-size: 20px;
            line-height: 30px;
            font-weight: 500;
            @media #{$mobile} {
              font-size: 16px;
              line-height: 20px;
            }
          }
          .store-code {
            margin: 15px 0 20px;
            font-size: 14px;
            line-height: 20px;
          }
          .msg {
            font-size: 14px;
            line-height: 20px;
            margin-top: 20px;
          }
        }
      }
      .close {
        position: absolute;
        width: 40px;
        height: 40px;
        border-radius: 50%;
        border: 1px solid #ccc;
        line-height: 12px;
        padding: 0 !important;
        opacity: 1;
        top: -55px;
        right: 15px;
        transition: all 0.3s ease-in-out;
        svg {
          transition: all 0.3s ease-in-out;
        }
        &:hover {
          opacity: .75;
          svg {
            transform: rotate(180deg);
          }
        }
      }
    }
  }
}
